import React from 'react';
import {PostData} from "../services/PostData";
import PageLogin from "../auth/pageLogin";



class ViewJob extends React.Component {
    constructor(props){
        super(props);
        this.state = {
            id:"",
            data:{employer:{},},
            error: null,
            preloader: false,
            jobs: [],
            loading:false,
            login:false,
        };
    };

    componentDidMount(){
        const { id } = this.props.match.params;
        this.setState({id:id});
        // fetch("http://127.0.0.1:8000/api/jobs/"+id+"/view")
        fetch("http://api.devjobs.ng/api/jobs/"+id+"/view")
            .then(res => res.json())
            .then(
                (result) => {
                    this.setState({
                        preloader: true,
                        data: result.data
                    });
                    // console.log(result.data)
                },
                (error) => {
                    console.log(error);
                }
            )
    }

    applyHandle=(e)=>{
        this.setState({loading:!this.state.loading});
        PostData("/api/jobs/"+(this.state.id)+"/apply").then((result)=>{
            this.setState({loading:!this.state.loading});
            if (result.status === 1){
                alert("You've successfully applied for this job!");

            }else if (result.status === 0){
                alert(result.error);
            }else {
                this.setState({login:!this.state.login});
            }

            // console.log(result)
        })
    };

    render() {
        const data = this.state.data;
        const {login, preloader,} = this.state;
        if (login) {
            return (
                <PageLogin/>
            );
        } else if (!preloader) {
            return (
                <div id="loader-wrapper">
                    <div id="loader"/>
                    <div className="loader-section section-left"/>
                    <div className="loader-section section-right"/>
                </div>

            );
        } else {
            const loading= this.state.loading;
            return (
                <div>
                    <Header name={data.employer.name}/>
                    <section>
                        <div className="block">
                            <div className="container">
                                <div className="row">
                                    <div className="col-lg-8 column">
                                        <div className="job-single-sec">
                                            <div className="job-single-head2">
                                                <div className="job-title2"><h3>{data.name}</h3><span
                                                    className="job-is ft">{data.job_type}</span></div>
                                                <ul className="tags-jobs">
                                                    <li><i className="la la-map-marker"/>{data.location}</li>
                                                    <li><i className="la la-money"/> Monthly Salary
                                                        : <span>{data.salary} - {data.salary_max}</span>
                                                    </li>
                                                    <li><i className="la la-calendar-o"/> Post Date: {data.date}</li>
                                                </ul>
                                            </div>
                                            <div className="job-details">
                                                <h3>Job Description</h3>
                                                <p>{data.description}</p>
                                            </div>
                                            <div className="job-details">
                                                <h3>Job Requirement</h3>
                                                <p>{data.requirement}</p>
                                            </div>
                                            <div className="job-overview">
                                                <h3>Job Overview</h3>
                                                <ul>
                                                    <li><i className="la la-money"/><h3>Offerd Salary</h3>
                                                        <span>{data.salary} - {data.salary_max}</span>
                                                    </li>
                                                    <li><i className="la la-thumb-tack"/><h3>Career Level</h3>
                                                        <span>{data.level}</span></li>
                                                    <li><i className="la la-shield"/><h3>Experience</h3>
                                                        <span>{data.experience}</span></li>
                                                </ul>
                                            </div>
                                            <div className="share-bar">
                                                <span>Share</span><a href="" title="" className="share-fb"><i className="fa fa-facebook"/></a><a href="" title="" className="share-twitter"><i className="fa fa-twitter"/></a>
                                            </div>
                                        </div>
                                    </div>
                                    <div className="col-lg-4 column">
                                        <div className="job-single-head style2">
                                            <div className="job-thumb"><img style={{width:124,height:124}} src={data.href.logo} alt=""/>
                                            </div>
                                            <div className="job-head-info">
                                                <h4>{data.employer.name}</h4>
                                                <span>{data.employer.location}</span>
                                                <p><i className="la la-unlink"/>{data.employer.website}</p>
                                            </div>
                                            <button onClick={this.applyHandle} className="apply-job-btn" type="submit" disabled={loading}>{loading?<i className="la la-spinner la-spin"/>:<i className="la la-paper-plane"/>} Apply for job</button>

                                            {/*<a onClick={this.applyHandle} className="apply-job-btn"><i className="la la-paper-plane"/>Apply for job</a>*/}
                                            {/*<a href="" title="" className="viewall-jobs">View all Jobs</a>*/}
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </section>

                </div>
            );
        }
    }
}

function Header(props) {
    return(
        <section className="overlape">
            <div className="block no-padding">
                <div className="container fluid">
                    <div className="row">
                        <div className="col-lg-12">
                            <div className="inner-header">
                                <h3>{props.name}</h3>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </section>
    )
}
export default ViewJob;


