import React from 'react';
import {PostData} from "../services/PostData";

class Contact extends React.Component {
    constructor(props){
            super(props);
            this.state = {
                loading:false,

            };
        };

    submitHandler=(e)=>{
        e.preventDefault();
        this.setState({loading:!this.state.loading});
        PostData('/api/employer/send_email', this.state).then((result)=>{
            this.setState({loading:!this.state.loading});
            console.log(result)
        })
    };
    onChange =(e) =>{
        this.setState({[e.target.name]:e.target.value});
    };
    render() {
        const loading= this.state.loading;
        return (
            <div className="quick-form-job">
                <h3>Contact</h3>
                <form onSubmit={this.submitHandler} >
                    <input type="text" onChange={this.onChange} name="subject" required placeholder="Subject" />
                    <textarea name="message" onChange={this.onChange} placeholder="Message..." required maxLength="100"/>
                    <button type="submit" disabled={loading}>{loading?<i className="la la-spinner la-spin"/>:""} Send Email</button>
                </form>
            </div>
        )
    }
}

export default Contact;


