import React from 'react';
import {Link} from "react-router-dom";

class RecentJob extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            error: null,
            isLoaded: false,
            jobs: []
        };
    }

    componentDidMount() {
        // fetch("http://127.0.0.1:8000/api/jobs/recent")
        fetch("http://api.devjobs.ng/api/jobs/recent")
            .then(res => res.json())
            .then(
                (result) => {
                    this.setState({
                        isLoaded: true,
                        jobs: result.data
                    });
                    console.log(result)
                },
                (error) => {
                    this.setState({
                        isLoaded: true,
                        error
                    });
                }
            );
    }

    render() {
        const { error, isLoaded, jobs } = this.state;
        if (error) {
            return(
                <section>
                    <div className="block gray">
                        <div className="container">
                            <div className="row">
                                <div className="col-lg-12">
                                    <div className="heading">
                                        <h2>Recent Jobs</h2>
                                        <span>Leading Employers already using job and talent.</span>
                                    </div>{/*<!-- Heading -->*/}
                                    <div className="job-grid-sec alert alert-danger">
                                        <h3 className="w3-text-red w3-center">Network failure!, please check your connection</h3>
                                    </div>

                                </div>
                            </div>
                        </div>
                    </div>
                </section>
            )
        } else if (!isLoaded) {
            return (
                <section>
                    <div className="block gray">
                        <div className="container">
                            <div className="row">
                                <div className="col-lg-12">
                                    <div className="heading">
                                        <h2>Recent Jobs</h2>
                                        <span>Leading Employers already using job and talent.</span>
                                    </div>{/*<!-- Heading -->*/}
                                    <div className="job-grid-sec w3-center">
                                        <img className="w3-center" src="../../images/loader-ajax.gif" alt=""/>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </section>
            )
        } else {
            return (
                <section>
                    <div className="block gray">
                    <div className="container">
                        <div className="row">
                            <div className="col-lg-12">
                                <div className="heading">
                                    <h2>Recent Jobs</h2>
                                    <span>Leading Employers already using job and talent.</span>
                                </div>{/*<!-- Heading -->*/}
                                <div className="job-grid-sec">
                                    <div className="row">
                                        {jobs.map(job => (
                                            <div className="col-lg-4 col-md-6 col-sm-6 col-xs-12">
                                                <div className="job-grid">
                                                    <div className="job-title-sec">
                                                        <div className="c-logo"> <img src="../../images/job_icon.png" alt="" /> </div>
                                                        <h3><Link to={"/job/"+(job.id)+"/"+(job.link)}>{job.name}</Link></h3>
                                                        {/*<span>{item.name}</span>*/}
                                                    </div>
                                                    <span className="job-lctn">{job.location}</span>
                                                    <Link to={"/job/"+(job.id)+"/"+(job.link)}>View Job</Link>
                                                </div>{/*<!-- JOB Grid -->*/}
                                            </div>
                                        ))}
                                    </div>
                                </div>

                            </div>
                            <div className="col-lg-12">
                                <div className="browse-all-cat">
                                    <Link to="/jobs" className="style2">Load more listings</Link>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
                </section>
            );
        }
    }

}

export default RecentJob;

{/*
<ul>
    {items.map(item => (
        <li key={item.name}>
            {item.name} {item.price}
        </li>
    ))}
</ul>*/}



