import React from 'react';
import {Link} from "react-router-dom";

class Hire extends React.Component {
    render() {
        return (
            <div>
                <section>
                    <div className="block no-padding">
                        <div className="parallax scrolly-invisible layer color"/>
                        <div className="container">
                            <div className="row">
                                <div className="col-lg-8 column">
                                    <div className="detailbar">
                                        <h3>Are You Hiring?</h3>
                                        <p>Find everything you need to post a job and receive the best candidates, <Link to="/signup">Sign up</Link> as an employer to get started. We offer small business and enterprise
                                            options.</p>
                                    </div>
                                </div>
                                <div className="col-lg-4 column">
                                    <div className="detalbr-mkp">
                                        <img src={"../../images/signup.png"} alt=""/>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </section>
            </div>
        )
    }
}

export default Hire;


