import React from 'react';
import {signUpData} from "../services/SignUp";
import {Link, Redirect} from "react-router-dom";
import Logout from "./logout";
import {GetData} from "../services/getData";

class SignUp extends React.Component {

    constructor(props) {
        super(props);
        this.state = {
            role: 1,
            email: "",
            username: "",
            password: "",
            password_confirmation: "",
            success:false,
            loading:false,
            errors:[],
        }

    }


    signUp =(e) =>{
        e.preventDefault();
        this.setState({loading:!this.state.loading});

        signUpData(this.state).then((result)=>{
            let authResult= result;
            if(authResult.access_token){
                this.setSession(authResult)
            }else{
                this.setState({loading:!this.state.loading});
                console.log(authResult.errors);
                this.setState({errors: authResult.errors})
            }
        })

    };

    setSession = (authResult) => {
        // Set the time that the access token will expire at
        let expiresAt = JSON.stringify((authResult.expires_in * 1000) + new Date().getTime());
        localStorage.setItem('auth',JSON.stringify(authResult.token_type+" "+authResult.access_token));
        localStorage.setItem('expires_at', expiresAt);
        this.getUser();
    };

    getUser=()=>{
        GetData('/user').then((result)=>{
            if (result.id) {
                localStorage.setItem('user', JSON.stringify(result));
                if (localStorage.getItem('user')){
                    window.location.reload();
                    // this.setState({loading:!this.state.loading});
                    // this.setState({success: true});
                }
            }else {
                Logout();
            }

        })
    };


    onClickCandidate = (e) =>{
        let employer = document.getElementById("employer");
        this.setState({role: 1});
        e.target.style.background="#fb236a";
        employer.style.background="#fff";
    };

    onClickEmployer = (e) =>{
        let candidate = document.getElementById("candidate");
        this.setState({role: 2});
        e.target.style.background="#fb236a";
        candidate.style.background="#fff";
    };

    onChange=(e)=>{
        this.setState({[e.target.name]:e.target.value});
    };

    render() {
        const loading= this.state.loading;
        return (
            <div className="account-popup-area signup-popup-box" style={{display:"block"}}>
                <div className="account-popup">
                    <Link to="/"><span className="close-popup"><i className="la la-close"/></span></Link>
                    <h3>Sign Up</h3>
                    <div className="select-user">
                        <span id="candidate" onClick={this.onClickCandidate}>Candidate</span>
                        <span id="employer" onClick={this.onClickEmployer}>Employer</span>
                    </div>
                    <form onSubmit={this.signUp}>
                        {this.state.errors.username?<span className="w3-text-red">{this.state.errors.username}</span>:""}
                        <div className="cfield">
                            <input type="text" max="10" placeholder="Username" required className={this.state.errors.username?"w3-border w3-round-large w3-border-red":""} name="username" onChange={this.onChange}/>
                            <i className="la la-user"/>
                        </div>

                        {this.state.errors.email?<span className="w3-text-red">{this.state.errors.email}</span>:""}
                        <div className="cfield">
                            <input type="text" placeholder="Email" required name='email' className={this.state.errors.email?"w3-border w3-round-large w3-border-red":""} onChange={this.onChange}/>
                            <i className="la la-envelope-o"/>
                        </div>

                        {this.state.errors.password?<span className="w3-text-red">{this.state.errors.password}</span>:""}
                        <div className="cfield">
                            <input type="password" placeholder="********" name='password' required className={this.state.errors.password?"w3-border w3-round-large w3-border-red":""} onChange={this.onChange}/>
                            <i className="la la-key"/>
                        </div>
                        <div className="cfield">
                            <input type="password" min="6" placeholder="********" required name='password_confirmation' className={this.state.errors.password?"w3-border w3-round-large w3-border-red":""} onChange={this.onChange} />
                            <i className="la la-key"/>
                        </div>
                        <button disabled={loading}>{loading?<i className="la la-spinner la-spin"/>:"Signup"}</button>
                        <div className="w3-right w3-margin-top">
                            Already have an account? <Link to="/login" className="w3-text-red">Login</Link>
                        </div>
                    </form>
                </div>
            </div>

        );
    }

}

export default SignUp;


