import React from 'react';
import ReactDOM from 'react-dom';
import { BrowserRouter as Router, Redirect} from 'react-router-dom';
import Route from 'react-router-dom/Route';
import Nav from "./components/layouts/nav";
import Home from "./components/index/home";
import Footer from "./components/layouts/footer";
import Login from "./components/auth/login";
import SignUp from "./components/auth/signup";
import CandidateProfile from "./components/candidate/Profile";
import SetProfileCandidate from "./components/candidate/SetProfile";
import SetProfileEmployer from "./components/employer/SetProfile";
import PostJob from "./components/employer/PostJob";
import ViewJob from "./components/job/view";
import './styles/bootstrap-grid.css';
import './styles/w3.css';
import './styles/icons.css';
import './styles/animate.min.css';
import './styles/style.css';
import './styles/responsive.css';
import './styles/chosen.css';
import './styles/colors/colors.css';

import CandidateDashboard from "./components/candidate/dashboard";
import EmployerDashboard from "./components/employer/dashboard";
import ManageJobs from "./components/employer/jobs";
import Payment from "./components/employer/payment";
import Jobs from "./components/job/list";
import EmployerProfile from "./components/employer/profile";
import Applicants from "./components/employer/applicants";
import Applied from "./components/candidate/applied";
import Resume from "./components/candidate/resume/resume";
import Verify from "./components/auth/verify";
import Package from "./components/package";
import Logout from "./components/auth/logout";
import Interview from "./components/employer/interview";
import MyPackage from "./components/employer/myPackage";
import SearchResult from "./components/index/searchResult";
import ChangePassword from "./components/employer/changePassword";
import Technology from "./components/candidate/technology";
import DirectCandidateProfile from "./components/candidateProfile";
import ApplicantInterview from "./components/employer/inviteApplicant";
import AddJob from "./components/employer/addjob";
import DirectVerify from "./components/auth/directVerify";


const user= JSON.parse(localStorage.getItem('user'));
const auth= JSON.parse(localStorage.getItem('auth'));

const RedirectPage = () => {
    return ( <Redirect to="/"/>)
};

const RedirectLogin = () => {
    return ( <Redirect to="/login"/>)
};

const RedirectActivate = () => {
    return ( <Redirect to="/activate"/>)
};

class Routes extends React.Component {
    state={
        expired:false
    };
    componentDidMount(){
        let expiresAt = localStorage.getItem('expires_at');
        if(auth && new Date().getTime() > expiresAt){
            this.setState({expired:true})
        }

        // console.log(new Date().getTime())
    }
    render() {
        return (
            <Router>
                <div>
                    {/*{user?user.status === 0?
                    <Route path="*" exact strict render={() => {return ( <Redirect to="/activate"/>);}
                    }/>:"":""}*/}
                    {this.state.expired?<Redirect to="/logout"/>:""}

                    <Nav role={user? user.role: ""} auth={auth?true:false} username={user? user.username:""}/>
                    <Route path="/" exact strict component={Home}/>
                    <Route path="/jobs" exact strict component={Jobs}/>
                    <Route path="/job/:id/:name" exact strict component={ViewJob}/>
                    <Route path="/search/result" exact strict component={SearchResult}/>
                    <Route path="/technology" exact strict component={Technology}/>




                    <Route path="/dashboard" exact strict component={
                        user?
                            user.status===0?RedirectActivate:
                                user.role === 1?CandidateDashboard: EmployerDashboard:
                            RedirectLogin
                    }/>
                    <Route path="/dashboard/jobs" exact strict component={
                        user?
                            user.status===0?RedirectActivate:
                                user.role === 2?ManageJobs: RedirectPage:
                            RedirectLogin
                    }/>
                    <Route path="/applied" exact strict component={
                        user?
                            user.status===0?RedirectActivate:
                                user.role === 1?Applied: RedirectPage:
                            RedirectLogin
                    }/>

                    <Route path="/profile" exact strict component={
                        user?
                            user.status===0?RedirectActivate:
                                user.role === 1?CandidateProfile: EmployerProfile:
                            RedirectLogin
                    }/>

                    <Route path="/profile/edit" exact strict component={
                        user?
                            user.status===0?RedirectActivate:
                                user.role === 1?SetProfileCandidate: SetProfileEmployer:
                            RedirectLogin
                    }/>

                    <Route path="/create_resume" exact strict component={
                        user?
                            user.status===0?RedirectActivate:
                                user.role === 1?Resume: RedirectPage:
                            RedirectLogin
                    }/>

                    <Route path="/applicants" exact strict component={
                        user?
                            user.status===0?RedirectActivate:
                                user.role === 2?Applicants: RedirectPage:
                            RedirectLogin
                    }/>

                    <Route path="/applicant/interview/:id" exact strict component={
                        user?
                            user.status===0?RedirectActivate:
                                user.role === 2?ApplicantInterview: RedirectPage:
                            RedirectLogin
                    }/>

                    <Route path="/interview" exact strict component={
                        user?
                            user.status===0?RedirectActivate:
                                user.role === 2?Interview: RedirectPage:
                            RedirectLogin
                    }/>



                    <Route path="/payment" exact strict component={
                        user?
                            user.status===0?RedirectActivate:
                                user.role === 2?Payment: RedirectPage:
                            RedirectLogin
                    }/>

                    <Route path="/package" exact strict component={
                        user?
                            user.status===0?RedirectActivate:
                                user.role === 2?Package: RedirectPage:
                            RedirectLogin
                    }/>

                    <Route path="/dashboard/package" exact strict component={
                        user?
                            user.status===0?RedirectActivate:
                                user.role === 2?MyPackage: RedirectPage:
                            RedirectLogin
                    }/>

                    <Route path="/dashboard/package/:id/add" exact strict component={
                        user?
                            user.status===0?RedirectActivate:
                                user.role === 2?AddJob: RedirectPage:
                            RedirectLogin
                    }/>

                    <Route path="/post_job" exact strict component={
                        user?
                            user.status===0?RedirectActivate:
                                user.role === 2?PostJob: RedirectPage:
                            RedirectLogin
                    }/>

                    <Route path="/change_password" exact strict component={
                        user?
                            user.status===0?RedirectActivate:
                                user.role === 1?CandidateDashboard: ChangePassword:
                            RedirectLogin
                    }/>

                    <Route path="/login" exact strict component={!user?Login: RedirectPage}/>

                    <Route path="/logout" exact strict component={auth?Logout: RedirectPage}/>

                    <Route path="/signup" exact strict component={!user?SignUp: RedirectPage}/>

                    <Route path="/activate" exact strict component={auth?Verify:RedirectPage}/>
                    <Route path="/activate/:code" exact strict component={auth?DirectVerify:RedirectPage}/>

                    <Route path="/:username/profile" exact strict component={DirectCandidateProfile}/>

                    {/*<Route path="*" exact strict component={NotFound}/>*/}
                    {/*<Route path="*" render={() => {return ( <Redirect to="/jobs"/>);}}/>*/}
                    <Footer/>
                </div>
            </Router>
        );
    }
}


ReactDOM.render(<Routes />, document.getElementById('root'));


