import React from 'react';
import EmployerHeader from "./layouts/header";
import Side from "./layouts/siderbar";
import {PostData} from "../services/PostData";



class ChangePassword extends React.Component {

    constructor(props){
        super(props);
        this.state = {
            confirm: "",
            // match:false,
            loading:false,
            password:"",
            errors:[],
        };
    };

    passwordHandler=(e)=>{
        e.preventDefault();
        this.setState({loading:!this.state.loading, errors:"",});
        PostData('/api/auth/change_password', this.state).then((result)=>{
            this.setState({loading:!this.state.loading});
            if(result.status === 1){
                this.setState({
                    old_password:"",
                    password:"",
                    password_confirmation:"",
                });
                alert(result.message)
            }else if(result.status === 0){
                this.setState({errors:{old_password:result.error}});
                alert(result.error)
            }else{
                this.setState({errors:result.errors})
            }
            console.log(result)
        })
    };

    onChange =(e) =>{
        this.setState({[e.target.name]:e.target.value});
    };

    validate =(e) =>{
        this.setState({confirm:e.target.value});
        // if (this.state.confirm === this.state.new_password){
        //     this.setState({match:true});
        // }
    };

    render() {
        const loading= this.state.loading;
        return (
            <div>
                <EmployerHeader/>
                <section>
                    <div className="block no-padding">
                        <div className="container">
                            <div className="row no-gape">
                                <Side/>
                                <div className="col-lg-9 column">
                                    <div className="padding-left">
                                        <div className="manage-jobs-sec">
                                            <h3>Change Password</h3>
                                            <div className="change-password">
                                                <form onSubmit={this.passwordHandler}>
                                                    <div className="row">
                                                        <div className="col-lg-6">
                                                            <span className="pf-title">Old Password</span>
                                                            <div className="pf-field">
                                                                <input className={this.state.errors.old_password?"w3-border w3-border-red":""} value={this.state.old_password} onChange={this.onChange} name="old_password" type="password"/>
                                                            </div>
                                                            <span className="pf-title">New Password</span>
                                                            <div className="pf-field">
                                                                <input className={this.state.errors.password?"w3-border w3-border-red":""} value={this.state.password} onChange={this.onChange} name="password" type="password"/>
                                                            </div>
                                                            <span className="pf-title">Confirm Password</span>
                                                            <div className="pf-field">
                                                                <input className={this.state.errors.password?"w3-border w3-border-red":""} value={this.state.password_confirmation} onChange={this.onChange} name="password_confirmation" type="password"/>
                                                            </div>
                                                            <button type="submit" disabled={loading}>{loading?<i className="la la-spinner la-spin"/>:""} Change</button>
                                                        </div>
                                                        <div className="col-lg-6">
                                                            <i className="la la-key big-icon"/>
                                                        </div>
                                                    </div>
                                                </form>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </section>
            </div>
        )
    }
}

export default ChangePassword;


