import React from 'react';
import CandidateHeader from "./layouts/header";
import Side from "./layouts/siderbar";
import {Redirect} from "react-router-dom";
import {PostData} from "../services/PostData";
import {GetData} from "../services/getData";
import Upload from "./fileUpload";

class SetProfileEmployer extends React.Component {
    constructor(props){
        super(props);
        this.state = {
            sectors: [],
            countries: [],
            states: [],
            success:false,
            profile:false,
            loading:false,
            facebook:"http://facebook.com/",
            twitter:"http://twitter.com/",
            google:"http://plus.google.com/",
            linkedin:"http://linkedin.com/",
            website:"http://",

        };
    };
    componentDidMount(){
        GetData("/employer/profile/edit").then((result)=>{
            if(result.status===1){
                this.setState({profile:true});
                this.setState(
                    result.data
                );
                this.setState(
                    result.social
                );

                // fetch("http://127.0.0.1:8000/api/client/states?country_id="+this.state.country_id)
                fetch("http://api.devjobs.ng/api/client/states?country_id="+this.state.country_id)
                    .then(res => res.json())
                    .then(
                        (result) => {
                            this.setState({
                                states: result.data
                            });
                        },
                    )
            }
        });
        // fetch("http://127.0.0.1:8000/api/client/countries")
        fetch("http://api.devjobs.ng/api/client/countries")
            .then(res => res.json())
            .then(
                (result) => {
                    this.setState({
                        countries: result.data
                    });
                },
                (error) => {
                    console.log()
                }
            );

        // fetch("http://127.0.0.1:8000/api/client/sectors")
        fetch("http://api.devjobs.ng/api/client/sectors")
            .then(res => res.json())
            .then(
                (result) => {
                    this.setState({
                        sectors: result.data
                    });
                },
                (error) => {
                    console.log()
                }
            )
    }

    formHandler = (e) => {
        e.preventDefault();
        this.setState({loading:!this.state.loading});
        if (this.state.profile){
            PostData('/api/employer/profile/edit',this.state).then((result)=>{
                this.setState({loading:!this.state.loading});
                let response = result;
                if(response.status === 1){
                    this.setState({success:true});
                    alert('Profile Updated Successfully!')
                }
                if(response.errors){
                    this.setState({errors: response.errors});
                    alert('Form validation error!')
                }
            })
        }else {
            PostData('/api/employer/profile',this.state).then((result)=>{
                this.setState({loading:!this.state.loading});
                let response = result;
                if(response.status === 1){
                    this.setState({success:true});
                    alert('Profile Updated Successfully!')
                }
                if(response.errors){
                    this.setState({errors: response.errors});
                    alert('Form validation error!')
                }
            })
        }

    };

    onChange =(e) =>{
        this.setState({[e.target.name]:e.target.value});
    };

    onChangeCountry =(e) =>{
        this.setState({country_id:e.target.value});
        const count= e.target.value;

        // fetch("http://127.0.0.1:8000/api/client/states?country_id="+count)
        fetch("http://api.devjobs.ng/api/client/states?country_id="+count)
            .then(res => res.json())
            .then(
                (result) => {
                    this.setState({
                        states: result.data
                    });
                },
                (error) => {
                    console.log()
                }
            )
    };

    render() {
        const countries = this.state.countries;
        const states = this.state.states;
        const sectors = this.state.sectors;
        const loading= this.state.loading;

        if(localStorage.getItem('auth')){
            return (
                <div>
                    <CandidateHeader/>
                    <section>
                        <div className="block no-padding">
                            <div className="container">
                                <div className="row no-gape">
                                    <Side/>
                                    <div className="col-lg-9 column">
                                        <div className="padding-left">
                                            <div className="profile-title">
                                                <h3>My Profile</h3>
                                            </div>
                                            <div className="profile-form-edit">
                                                <form>
                                                    <div className="row">
                                                        <div className="col-lg-6">
                                                            <span className="pf-title">Full Name</span>
                                                            <div className="pf-field">
                                                                <input type="text" name="name" onChange={this.onChange} value={this.state.profile? this.state.name:""} placeholder="AeroJet Global Enterprice" />
                                                            </div>
                                                        </div>
                                                        <div className="col-lg-6">
                                                            <span className="pf-title">Sector</span>
                                                            <div className="pf-field">
                                                                <select onChange={this.onChange} name="sector_id" className="chosen w3-select w3-border w3-round-large">
                                                                    <option>Select</option>
                                                                    {sectors.map(sector => (
                                                                        <option selected={this.state.sector_id} value={sector.id}>{sector.name}</option>
                                                                    ))}
                                                                </select>
                                                            </div>
                                                        </div>

                                                        <div className="col-lg-6">
                                                            <span className="pf-title">Country</span>
                                                            <div className="pf-field">
                                                                <select name="country_id" onChange={this.onChangeCountry} required className="w3-select w3-border w3-round-large chosen">
                                                                    <option>Select Country</option>
                                                                    {countries.map(country => (
                                                                        <option selected={this.state.country_id === country.id} value={country.id}>{country.name}</option>
                                                                    ))}
                                                                </select>
                                                            </div>
                                                        </div>
                                                        <div className="col-lg-6">
                                                            <span className="pf-title">State</span>
                                                            <div className="pf-field">
                                                                <select name="state_id" onChange={this.onChange} required className="w3-select w3-border w3-round-large chosen">
                                                                    <option>Select State</option>
                                                                    {states.map(state => (
                                                                        <option selected={this.state.state_id ===state.id} value={state.id}>{state.name}</option>
                                                                    ))}
                                                                </select>
                                                            </div>
                                                        </div>
                                                        <div className="col-lg-12">
                                                            <span className="pf-title">About</span>
                                                            <div className="pf-field">
                                                                <textarea name="about" value={this.state.about} onChange={this.onChange}/>
                                                            </div>
                                                        </div>
                                                    </div>
                                                </form>
                                            </div>
                                            <div className="social-edit">
                                                <h3>Social Edit</h3>
                                                <form>
                                                    <div className="row">
                                                        <div className="col-lg-6">
                                                            <span className="pf-title">Facebook</span>
                                                            <div className="pf-field">
                                                                <input type="text" name="facebook" value={this.state.facebook} onChange={this.onChange} placeholder="http://www.facebook.com/devjobs" />
                                                                <i className="fa fa-facebook"/>
                                                            </div>
                                                        </div>
                                                        <div className="col-lg-6">
                                                            <span className="pf-title">Twitter</span>
                                                            <div className="pf-field">
                                                                <input type="text" name="twitter" value={this.state.twitter} onChange={this.onChange} placeholder="http://www.twitter.com/devjobs" />
                                                                <i className="fa fa-twitter"/>
                                                            </div>
                                                        </div>
                                                        <div className="col-lg-6">
                                                            <span className="pf-title">Google</span>
                                                            <div className="pf-field">
                                                                <input type="text" name="google" value={this.state.google} onChange={this.onChange} placeholder="http://www.google-plus.com/devjobs" />
                                                                <i className="la la-google"/>
                                                            </div>
                                                        </div>
                                                        <div className="col-lg-6">
                                                            <span className="pf-title">Linkedin</span>
                                                            <div className="pf-field">
                                                                <input type="text" name="linkedin" value={this.state.linkedin} onChange={this.onChange} placeholder="http://www.linkedin.com/devjobs" />
                                                                <i className="la la-linkedin"/>
                                                            </div>
                                                        </div>
                                                    </div>
                                                </form>
                                            </div>
                                            <div className="contact-edit">
                                                <h3>Contact</h3>
                                                <form onSubmit={this.formHandler}>
                                                    <div className="row">
                                                        <div className="col-lg-4">
                                                            <span className="pf-title">Phone Number</span>
                                                            <div className="pf-field">
                                                                <input name='phone' onChange={this.onChange} value={this.state.phone} type="text" placeholder="+90 538 963 58 96" />
                                                            </div>
                                                        </div>

                                                        <div className="col-lg-4">
                                                            <span className="pf-title">Website</span>
                                                            <div className="pf-field">
                                                                <input type="text" name="website" value={this.state.website} placeholder="http://www.devjobs.ng" />
                                                            </div>
                                                        </div>
                                                        <div className="col-lg-12">
                                                            <button type="submit" disabled={loading}>{loading?<i className="la la-spinner la-spin"/>:""} Update</button>
                                                        </div>
                                                    </div>
                                                </form>
                                            </div>
                                            <Upload/>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </section>
                </div>
            );
        }else{
            return (<Redirect to={'/'}/>)
        }

    }
}



export default SetProfileEmployer;


