import React from 'react';
import {PostData} from "../../services/PostData";
import {Redirect} from "react-router-dom";


class JobHeader extends React.Component {
    constructor(props){
        super(props);
        this.state = {
            jobs:[],
            loading:false,
            redirect:false,
        };
    };

    searchHandler=(e)=>{
        e.preventDefault();
        this.setState({loading:!this.state.loading});
        PostData('/api/jobs',this.state).then((result)=>{
            this.setState({
                loading:!this.state.loading,
                jobs:result.data,
            });
            if(this.props.refresh){
                this.refresh();
            }else {
                this.setState({
                    redirect:true
                });
            }

            console.log(result);
            sessionStorage.setItem('result',JSON.stringify(result.data));
        })
    };

    refresh(){
        window.location.reload()
    }

    onChange=(e)=>{
        this.setState({
            [e.target.name]:e.target.value
        })
    };
    render() {
        const loading= this.state.loading;
        if (this.state.redirect){
            return <Redirect to="/search/result"/>
        }
        return (
            <section className="overlape">
                <div className="block no-padding">
                    <div className="container fluid">
                        <div className="row">
                            <div className="col-lg-12">
                                <div className="inner-header wform">
                                    <div className="job-search-sec">
                                        <div className="job-search">
                                            <h4>Explore Thousand Of Jobs With Just Simple Search...</h4>
                                            <form onSubmit={this.searchHandler}>
                                                <div className="row">
                                                    <div className="col-lg-7">
                                                        <div className="job-field">
                                                            <select name="category_id" onChange={this.onChange} className="chosen-city select-input">
                                                                <option value="0">Select Category</option>
                                                                <option value="1">UX/UI</option>
                                                                <option value="2">Frontend Developer</option>skill
                                                                <option value="3">Backend Developer</option>
                                                                <option value="4">Fullstack </option>
                                                                <option value="5">QA </option>
                                                                <option value="6">DevOps </option>
                                                                <option value="7">Mobile App (iOS) </option>
                                                                <option value="8">Mobile App (Android) </option>
                                                                <option value="9">Software Developer </option>
                                                                <option value="10">Product Manager </option>
                                                                <option value="11">Project Manager </option>
                                                                <option value="12">Business Analyst </option>
                                                                <option value="13">Hybrid Mobile Dev </option>
                                                                <option value="14">Database Admin </option>
                                                                <option value="15">Others </option>
                                                            </select>
                                                            <i className="la la-keyboard-o"/>
                                                        </div>
                                                    </div>
                                                    <div className="col-lg-4">
                                                        <div className="job-field">
                                                            <select name="level_id" onChange={this.onChange} className="chosen-city select-input">
                                                                <option value="0">Select Level</option>
                                                                <option value="1">Intern</option>
                                                                <option value="2">Junior</option>
                                                                <option value="3">Intermediate </option>
                                                                <option value="4">Advance</option>
                                                                <option value="5">Senior </option>
                                                            </select>
                                                            <i className="la la-keyboard-o"/>
                                                        </div>
                                                    </div>
                                                    <div className="col-lg-1">
                                                        <button type="submit" disabled={loading}>{loading?<i className="la la-spinner la-spin"/>:<i className="la la-search"/>}</button>
                                                    </div>
                                                </div>
                                            </form>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </section>
        );
    }
}

export default JobHeader;


