import React from 'react';
import EmployerHeader from "./layouts/header";
import {PostData} from "../services/PostData";

class ApplicantInterview extends React.Component {

    constructor(props){
        super(props);
        this.state = {
            name:"",
            loading:false,
        };
    };

    inviteHandler=(e)=>{
        e.preventDefault();
        this.setState({loading:!this.state.loading});
        const { id } = this.props.match.params;
        this.setState({inviteId:e.target.id});
        PostData('/api/jobs/applicant/'+id+'/invite', this.state).then((result)=>{
            this.setState({loading:!this.state.loading});
            if (result.status===1){
                alert(result.message);
            }
        })
    };

    onChange =(e) =>{
        this.setState({[e.target.name]:e.target.value});
    };


    render() {
        const loading= this.state.loading;
        return (
            <div>
                <EmployerHeader/>
                <div className="container" style={{display:'block'}}>
                    <div className="w3-modal-content w3-white" style={{maxWidth:500}}>
                        <div className="w3-container w3-padding-large">
                            <div className="w3-margin profile-form-edit">
                                <h3>Invite {this.props.name}</h3>
                                <form onSubmit={this.inviteHandler}>
                                    <div className="pf-field">
                                        <span className="pf-title">Address or Link</span>
                                        <input type="text" name="address" onChange={this.onChange}/>
                                    </div>
                                    <div className="pf-field">
                                        <span className="pf-title">Date</span>
                                        <input type="date" name="date" onChange={this.onChange}/>
                                    </div>
                                    <div className="pf-field">
                                        <span className="pf-title">Time</span>
                                        <input type="time" name="time" onChange={this.onChange}/>
                                    </div>
                                    <button type="submit" disabled={loading}>{loading?<i className="la la-spinner la-spin"/>:""} Send</button>
                                </form>

                            </div>
                        </div>
                    </div>
                </div>
            </div>
        )
    }
}

export default ApplicantInterview;


