import React, { Component } from 'react';
import PaystackButton from 'react-paystack';
import Side from "./layouts/siderbar";
import EmployerHeader from "./layouts/header";
import {GetData} from "../services/getData";
import {PostData} from "../services/PostData";
import {Link} from "react-router-dom";
import {FetchGet} from "../services/fetchget";

const user= JSON.parse(localStorage.getItem('user'));

class Payment extends Component {

    constructor(props){
        super(props);
        this.state = {
            key: "pk_test_a8bd29bb7432d9f2f9e973c85be77fefecf2b1b7",
            email: user.email,  // customer email
            amount: 0,
            jobs:[],
            id:[],
            preloader: false,
        };
    };

    callback = (response) => {
        console.log(response);
        if(response.reference){
            FetchGet('https://api.paystack.co/transaction/verify/'+response.reference).then((result)=>{
                this.setState({payment:result});
                if (result.data.status === "success"){
                    console.log(result);
                    this.process();
                }else{
                    this.setState({success:false});
                }
            });
        }
    };

    process(){
        PostData('/api/employer/direct_activate', this.state.id).then((result)=>{
            console.log(result);
            if (result.status===1){
                this.setState({
                    message:result.message,
                    success:true
                });
                alert(result.message);
                window.location.reload();
            }else {
                console.log(result)
            }
        });
    }
    close = () => {
        alert('Payment closed');
        console.log("Payment closed");
    };

    getReference = () => {
        //you can put any unique reference implementation code here
        let text = "";
        let possible = "ABCDEFGHIJKLMNOPQRSTUVWXYZabcdefghijklmnopqrstuvwxyz0123456789-.=";

        for( let i=0; i < 15; i++ )
            text += possible.charAt(Math.floor(Math.random() * possible.length));

        return text;
    };

    componentDidMount(){
        GetData("/jobs/employer/pending").then((result)=>{
            if(result.status===1){
                this.setState({jobs:result.data});
                result.data.map(job =>(

                    this.setState({id: this.state.id.concat(""+job.id+"")})
                ));
                this.checkHandler();
            }
        });

    }

    checkHandler=(e)=>{
        let num= this.state.jobs.length;
        // this.setState({id: this.state.id.concat(e.target.value)});

        this.setState({amount: num * 1000000});
        this.setState({preloader: true});

    };


    addJob=(e)=>{
        this.setState({id: this.state.id.concat(e.target.value)});
        this.setState((prevState) => {
            return {amount: (prevState.amount + 1000000)}
        });

    };
    removeJob=(e)=>{
        let id = this.state.id;
        let index = id.indexOf(e.target.value);
        id.splice(index,1);
        this.setState({id: id});

        this.setState((prevState) => {
            return {amount: (prevState.amount - 1000000)}
        });


    };


    render() {
        const jobs = this.state.jobs;
        let amount = this.state.amount / 100;
        const {error, preloader} = this.state;
        if (error) {
            return <div>Error</div>;
        } else if (!preloader) {
            return (
                <div id="loader-wrapper">
                    <div id="loader"/>
                    <div className="loader-section section-left"/>
                    <div className="loader-section section-right"/>
                </div>
            );
        } else {
            return (
                <div>
                    <EmployerHeader/>
                    <section>
                        <div className="block no-padding">
                            <div className="container">
                                <div className="row no-gape">
                                    <Side/>
                                    <div className="col-lg-9 column">
                                        <div className="padding-left">
                                            <div className="profile-title">
                                                <h3>Make Payment</h3>
                                                <div className="steps-sec">
                                                    <div className="step">
                                                        <p><i className="la la-info"/></p>
                                                        <span>Information</span>
                                                    </div>
                                                    <div className="step active">
                                                        <p><i className="la la-cc-mastercard"/></p>
                                                        <span>Payments</span>
                                                    </div>
                                                    <div className="step">
                                                        <p><i className="la  la-check-circle"/></p>
                                                        <span>Done</span>
                                                    </div>
                                                </div>

                                                <div className="manage-jobs-sec">
                                                    <table>
                                                        <thead>
                                                        <tr>
                                                            <td>Title</td>
                                                            <td>Status</td>
                                                            <td>Action</td>
                                                        </tr>
                                                        </thead>
                                                        <tbody>
                                                        {jobs.map(job => (
                                                            <tr>
                                                                <td>
                                                                    {this.state.id.includes("" + job.id + "") ?
                                                                        <input type="checkbox" checked
                                                                               onClickCapture={this.removeJob}
                                                                               value={job.id} className="w3-check"/>
                                                                        : <input type="checkbox" onChange={this.addJob}
                                                                                 value={job.id} className="w3-check"/>
                                                                    }
                                                                </td>
                                                                <td>
                                                                    <div className="table-list-title">
                                                                        <h3><a href="" title="">{job.name}</a></h3>
                                                                    </div>
                                                                </td>
                                                                <td>
                                                                    <span>Pending</span>
                                                                </td>
                                                                <td>
                                                                    <ul className="action_job">
                                                                        {/*<li><span>Edit</span><a href="" title=""><i className="la la-pencil"></i></a></li>*/}
                                                                        <li><span>Delete</span><a href="" title=""><i className="la la-trash-o"/></a></li>
                                                                    </ul>
                                                                </td>
                                                            </tr>
                                                        ))}
                                                        </tbody>
                                                    </table>
                                                </div>
                                            </div>

                                            <p>
                                                <h3>&#8358; {amount.toFixed(2).replace(/(\d)(?=(\d{3})+\.)/g, '$1,')}</h3>
                                                <div className="w3-right w3-margin-bottom">
                                                    <PaystackButton
                                                        class="w3-btn w3-green w3-padding-large w3-round-xxlarge"
                                                        callback={this.callback}
                                                        close={this.close}
                                                        disabled={false}
                                                        embed={false}
                                                        reference={this.getReference()}
                                                        email={this.state.email}
                                                        amount={this.state.amount}
                                                        paystackkey={this.state.key}
                                                    />

                                                    &nbsp;
                                                    <strong>OR</strong>
                                                    &nbsp;

                                                    <span>
                                                    <Link to="/dashboard/package" className="w3-btn w3-blue w3-padding-large w3-round-xxlarge">Add to Package Plan</Link>
                                                </span>
                                                </div>
                                            </p>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </section>
                </div>

            );
        }
    }
}

export default Payment;


