import React from 'react';
import CandidateHeader from "./layouts/header";
import Side from "./layouts/siderbar";
import {Redirect} from "react-router-dom";
import {PostData} from "../services/PostData";
import {GetData} from "../services/getData";
import Upload from "./fileUpload";
import Technology from "./technology";

class SetProfileCandidate extends React.Component {
    constructor(props){
        super(props);
        this.state = {
            name:"",
            errors: [],
            countries: [],
            country_id:160,
            states: [],
            success:false,
            profile:false,
            bio:"",
            loading:false,
            facebook:"http://facebook.com/",
            twitter:"http://twitter.com/",
            google:"http://plus.google.com/",
            linkedin:"http://linkedin.com/",
            preloader: false,

        };
    };
    componentDidMount(){
        GetData("/candidate/profile/edit").then((result)=>{
            if(result.status === 1){
                this.setState(
                    result.data
                );
                this.setState({profile:true,});
                // this.setState(
                //     result.social
                // );
                // console.log(result);

                // fetch("http://127.0.0.1:8000/api/client/states?country_id="+this.state.country_id)
                fetch("http://api.devjobs.ng/api/client/states?country_id="+this.state.country_id)
                    .then(res => res.json())
                    .then(
                        (result) => {
                            this.setState({
                                states: result.data,
                                preloader:true,
                            });
                        },
                    )
            }else{
                // fetch("http://127.0.0.1:8000/api/client/countries")
                fetch("http://api.devjobs.ng/api/client/countries")
                    .then(res => res.json())
                    .then(
                        (result) => {
                            this.setState({
                                countries: result.data,
                                preloader:true,

                            });
                        }
                    );

                fetch("http://api.devjobs.ng/api/client/states?country_id="+160)
                    .then(res => res.json())
                    .then(
                        (result) => {
                            this.setState({
                                states: result.data,
                                preloader:true,
                            });
                        },
                    )
            }




        });

    }

    formHandler = (e) => {
        e.preventDefault();

        this.setState({loading:!this.state.loading});
        if (this.state.profile){
            PostData('/api/candidate/profile/edit',this.state).then((result)=>{
                this.setState({loading:!this.state.loading});
                let response = result;
                if(response.status === 1){
                    this.setState({success:true});
                    alert('Profile Updated Successfully!')
                }
                if(response.errors){
                    this.setState({errors: response.errors});
                    alert('Form validation error!')
                }
            })
        }else {
            PostData('/api/candidate/profile',this.state).then((result)=>{
                this.setState({loading:!this.state.loading});
                let response = result;
                if(response.status === 1){
                    this.setState({success:true, profile:true,});
                    alert('Profile Updated Successfully!')
                }
                if(response.errors){
                    this.setState({errors: response.errors});
                    alert('Form validation error!')
                }
            })
        }
    };

    onChange =(e) =>{
        this.setState({[e.target.name]:e.target.value});
    };

    onChangeCountry =(e) =>{
        this.setState({country_id:e.target.value});
        const count= e.target.value;
        // fetch("http://127.0.0.1:8000/api/client/states?country_id="+count)
        fetch("http://api.devjobs.ng/api/client/states?country_id="+count)

            .then(res => res.json())
            .then(
                (result) => {
                    this.setState({
                        states: result.data
                    });
                },
            )
    };

    render() {
        if(localStorage.getItem('auth')) {
            const {error, preloader, states, countries, loading} = this.state;
            if (error) {
                return <div>Error</div>;
            } else if (!preloader) {
                return (
                    <div id="loader-wrapper">
                        <div id="loader"/>
                        <div className="loader-section section-left"/>
                        <div className="loader-section section-right"/>
                    </div>

                );
            } else {
                return (
                    <div>
                        <CandidateHeader/>
                        <section>
                            <div className="block no-padding">
                                <div className="container">
                                    <div className="row no-gape">
                                        <Side/>
                                        <div className="col-lg-9 column">
                                            <div className="padding-left">
                                                <div className="profile-title">
                                                    <h3>My Profile</h3>
                                                    <div className="upload-img-bar">
                                                    </div>
                                                </div>
                                                <div className="profile-form-edit">
                                                    <form>
                                                        <div className="row">
                                                            <div className="col-lg-6">
                                                                <span className="pf-title">Full Name</span>
                                                                <div className="pf-field">
                                                                    <input type="text" name="name"
                                                                           onChange={this.onChange}
                                                                           className={(this.state.errors.name ? "w3-border-red" : "")}
                                                                           value={this.state.name}
                                                                           placeholder="Oluwayemi Okoro Muhammed"/>
                                                                </div>
                                                            </div>
                                                            <div className="col-lg-6">
                                                                <span className="pf-title">Skill Category</span>
                                                                <div className="pf-field">
                                                                    <select onChange={this.onChange} name="category_id"
                                                                            className={"w3-select w3-border w3-round-large" + " " + (this.state.errors.category_id ? "w3-border-red" : "")}>
                                                                        <option value="">Select</option>
                                                                        <option selected={this.state.category_id === 1}
                                                                                value="1">UX/UI
                                                                        </option>
                                                                        <option selected={this.state.category_id === 2}
                                                                                value="2">Frontend Developer
                                                                        </option>
                                                                        skill
                                                                        <option selected={this.state.category_id === 3}
                                                                                value="3">Backend Developer</option>
                                                                        <option selected={this.state.category_id === 4}
                                                                                value="4">Fullstack
                                                                        </option>
                                                                        <option selected={this.state.category_id === 5}
                                                                                value="5">QA
                                                                        </option>
                                                                        <option selected={this.state.category_id === 6}
                                                                                value="6">DevOps
                                                                        </option>
                                                                        <option selected={this.state.category_id === 7}
                                                                                value="7">Mobile App (iOS)
                                                                        </option>
                                                                        <option selected={this.state.category_id === 8}
                                                                                value="8">Mobile App (Android)
                                                                        </option>
                                                                        <option selected={this.state.category_id === 9}
                                                                                value="9">Software Developer
                                                                        </option>
                                                                        <option selected={this.state.category_id === 10}
                                                                                value="10">Product Manager
                                                                        </option>
                                                                        <option selected={this.state.category_id === 11}
                                                                                value="11">Project Manager
                                                                        </option>
                                                                        <option selected={this.state.category_id === 12}
                                                                                value="12">Business Analyst
                                                                        </option>
                                                                        <option selected={this.state.category_id === 13}
                                                                                value="13">Hybrid Mobile Dev
                                                                        </option>
                                                                        <option selected={this.state.category_id === 14}
                                                                                value="14">Database Admin
                                                                        </option>
                                                                        <option selected={this.state.category_id === 15}
                                                                                value="15">Others
                                                                        </option>
                                                                    </select>
                                                                </div>
                                                            </div>
                                                            <div className="col-lg-6">
                                                                <span className="pf-title">Skill Level</span>
                                                                <div className="pf-field">
                                                                    <select onChange={this.onChange} name="level_id"
                                                                            className={"w3-select w3-border w3-round-large" + " " + (this.state.errors.level_id ? "w3-border-red" : "")}>
                                                                        <option value="">Select</option>
                                                                        <option selected={this.state.level_id === 1}
                                                                                value="1">Intern
                                                                        </option>
                                                                        <option selected={this.state.level_id === 2}
                                                                                value="2">Junior
                                                                        </option>
                                                                        <option selected={this.state.level_id === 3}
                                                                                value="3">Intermediate
                                                                        </option>
                                                                        <option selected={this.state.level_id === 4}
                                                                                value="4">Advance
                                                                        </option>
                                                                        <option selected={this.state.level_id === 5}
                                                                                value="5">Senior
                                                                        </option>
                                                                    </select>
                                                                </div>
                                                            </div>

                                                            <div className="col-lg-6">
                                                                <span className="pf-title">Experience</span>
                                                                <div className="pf-field">
                                                                    <select onChange={this.onChange} name="experience"
                                                                            className={"w3-select w3-border w3-round-large" + " " + (this.state.errors.experience ? "w3-border-red" : "")}>
                                                                        <option value="">Select</option>
                                                                        <option
                                                                            selected={this.state.experience === "0-1 Year"}>0-1
                                                                            Year
                                                                        </option>
                                                                        <option
                                                                            selected={this.state.experience === "2-5 Years"}>2-5
                                                                            Years
                                                                        </option>
                                                                        <option
                                                                            selected={this.state.experience === "6-12 Years"}>6-12
                                                                            Years
                                                                        </option>
                                                                        <option
                                                                            selected={this.state.experience === "13-20 Years"}>13-20
                                                                            Years
                                                                        </option>
                                                                    </select>
                                                                </div>
                                                            </div>
                                                            <div className="col-lg-6">
                                                                <span className="pf-title">Date Of Birth</span>
                                                                <div className="pf-field">
                                                                    <div className="pf-field">
                                                                        <input name="dob" value={this.state.dob}
                                                                               className={(this.state.errors.dob ? "w3-border-red" : "")}
                                                                               onChange={this.onChange} type="date"
                                                                               placeholder="30k"/>
                                                                    </div>
                                                                </div>
                                                            </div>
                                                            <div className="col-lg-6">
                                                                <span className="pf-title">Sex</span>
                                                                <div className="pf-field">
                                                                    <select onChange={this.onChange} name="sex"
                                                                            className={"w3-select w3-border w3-round-large" + " " + (this.state.errors.sex ? "w3-border-red" : "")}>
                                                                        <option>Select</option>
                                                                        <option
                                                                            selected={this.state.sex === 'Male'}>Male
                                                                        </option>
                                                                        <option
                                                                            selected={this.state.sex === 'Female'}>Female
                                                                        </option>
                                                                    </select>
                                                                </div>
                                                            </div>

                                                            <div className="col-lg-6">
                                                                <span className="pf-title">Country</span>
                                                                <div className="pf-field">
                                                                    <select name="country_id"
                                                                            onChange={this.onChangeCountry}
                                                                            className={"w3-select w3-border w3-round-large" + " " + (this.state.errors.country_id ? "w3-border-red" : "")}>
                                                                        <option>Select Country</option>
                                                                        {countries.map(country => (
                                                                            <option
                                                                                selected={this.state.country_id === country.id || country.id === 160}
                                                                                value={country.id}>{country.name}</option>
                                                                        ))}
                                                                    </select>
                                                                </div>
                                                            </div>
                                                            <div className="col-lg-6">
                                                                <span className="pf-title">State</span>
                                                                <div className="pf-field">
                                                                    <select name="state_id" onChange={this.onChange}
                                                                            className={"w3-select w3-border w3-round-large" + " " + (this.state.errors.state_id ? "w3-border-red" : "")}>
                                                                        <option>Select State</option>
                                                                        {states.map(state => (
                                                                            <option
                                                                                selected={this.state.state_id === state.id}
                                                                                value={state.id}>{state.name}</option>
                                                                        ))}
                                                                    </select>
                                                                </div>
                                                            </div>
                                                            <div className="col-lg-12">
                                                                <span className="pf-title">Bio</span>
                                                                <div className="pf-field">
                                                                    <textarea
                                                                        className={(this.state.errors.bio ? "w3-border-red" : "")}
                                                                        name="bio" onChange={this.onChange}
                                                                        value={this.state.bio}/>
                                                                </div>
                                                            </div>
                                                        </div>
                                                    </form>
                                                </div>
                                                <div className="social-edit">
                                                    <h3>Social Edit</h3>
                                                    <form>
                                                        <div className="row">
                                                            <div className="col-lg-6">
                                                                <span className="pf-title">Facebook</span>
                                                                <div className="pf-field">
                                                                    <input type="text" name="facebook"
                                                                           value={this.state.facebook}
                                                                           onChange={this.onChange}
                                                                           className={(this.state.errors.facebook ? "w3-border-red" : "")}
                                                                           placeholder="www.facebook.com/devjobs"/>
                                                                    <i className="fa fa-facebook"/>
                                                                </div>
                                                            </div>
                                                            <div className="col-lg-6">
                                                                <span className="pf-title">Twitter</span>
                                                                <div className="pf-field">
                                                                    <input type="text" name="twitter"
                                                                           value={this.state.twitter}
                                                                           onChange={this.onChange}
                                                                           className={(this.state.errors.twitter ? "w3-border-red" : "")}
                                                                           placeholder="www.twitter.com/devjobs"/>
                                                                    <i className="fa fa-twitter"/>
                                                                </div>
                                                            </div>
                                                            <div className="col-lg-6">
                                                                <span className="pf-title">Google</span>
                                                                <div className="pf-field">
                                                                    <input type="text" name="google"
                                                                           value={this.state.google}
                                                                           onChange={this.onChange}
                                                                           className={(this.state.errors.google ? "w3-border-red" : "")}
                                                                           placeholder="www.google-plus.com/devjobs"/>
                                                                    <i className="la la-google"/>
                                                                </div>
                                                            </div>
                                                            <div className="col-lg-6">
                                                                <span className="pf-title">Linkedin</span>
                                                                <div className="pf-field">
                                                                    <input type="text" name="linkedin"
                                                                           value={this.state.linkedin}
                                                                           onChange={this.onChange}
                                                                           className={(this.state.errors.linkedin ? "w3-border-red" : "")}
                                                                           placeholder="www.linkedin.com/devjobs"/>
                                                                    <i className="la la-linkedin"/>
                                                                </div>
                                                            </div>
                                                        </div>
                                                    </form>
                                                </div>
                                                <div className="contact-edit">
                                                    <h3>Contact</h3>
                                                    <form onSubmit={this.formHandler}>
                                                        <div className="row">
                                                            <div className="col-lg-4">
                                                                <span className="pf-title">Phone Number</span>
                                                                <div className="pf-field">
                                                                    <input name='phone'
                                                                           className={(this.state.errors.phone ? "w3-border-red" : "")}
                                                                           value={this.state.phone}
                                                                           onChange={this.onChange} type="text"
                                                                           placeholder="+90 538 963 58 96"/>
                                                                </div>
                                                            </div>
                                                            <div className="col-lg-12">
                                                                <button type="submit" disabled={loading}>{loading ?
                                                                    <i className="la la-spinner la-spin"/> : ""} Update
                                                                </button>
                                                            </div>
                                                        </div>
                                                    </form>
                                                </div>
                                                <Technology/>
                                                <Upload/>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </section>
                    </div>
                );
            }
        }
    }
}



export default SetProfileCandidate;


