import React from 'react';
import {PostData} from "../services/PostData";

class Upload extends React.Component {
    constructor(props){
        super(props);
        this.state = {
            loading:false,

        };
    };

    imageHandler=(e)=>{

        const file= e.target.files[0];
        return new Promise((resolve, reject) => {
            const reader = new FileReader();
            reader.readAsDataURL(file);
            reader.onload = () => this.setState({image:reader.result});
            reader.onerror = error => reject(error);
        });

    };
    uploadHandler=(e)=>{
        e.preventDefault();
        this.setState({loading:!this.state.loading});
        PostData('/api/employer/upload', this.state).then((result)=>{
            this.setState({loading:!this.state.loading});
            if(result.status === 1){
                alert(result.message)
            }else{
                alert(result.error)
            }
            console.log(result)
        })
    };

    render() {
        const loading= this.state.loading;
        return (
            <div className="contact-edit">
                <h3>Upload Files</h3>
                <form onSubmit={this.uploadHandler}>
                    <div className="row">
                        <div className="col-lg-4">
                            <span className="pf-title">Profile Logo</span>
                            <div className="pf-field">
                                <input type="file" onChange={this.imageHandler} name="image" className="w3-input w3-border"/>
                            </div>
                        </div>
                        <div className="col-lg-12">
                            <button type="submit" disabled={loading}>{loading?<i className="la la-spinner la-spin"/>:""} Upload</button>
                        </div>
                    </div>
                </form>
            </div>
        );
    }
}

export default Upload;


