import React from 'react';
import EmployerHeader from "./layouts/header";
import Side from "./layouts/siderbar";
import {GetData} from "../services/getData";
import {FetchPost} from "../services/fectchPost";
import {PostData} from "../services/PostData";


class AddJob extends React.Component {

    constructor(props){
        super(props);
        this.state = {
            jobs:[],
            preloader: false,
            job_id:[],
            disable:false,
        };
    };

    componentDidMount(){
        const { id } = this.props.match.params;
        this.setState({id:id});
        FetchPost("/api/employer/plan/check/"+id).then((result)=>{
            if (result.status===1){
                if (result.limit < 1){
                    this.setState({limit:result.limit, disable:true});
                }else {
                    this.setState({limit:result.limit});
                }
                this.setState({limit:result.limit});
                GetData("/jobs/employer/pending").then((result)=>{
                    if(result.status===1){
                        this.setState({jobs:result.data,preloader: true,})
                    }
                    else {
                        alert(result.error)
                    }
                    console.log(result.data)
                });
            }
            else {
                alert("Plan does not exist!")
            }
        })
    }



    addJob=(e)=>{
        if (this.state.job_id.length <= this.state.limit){
            this.setState({job_id: this.state.job_id.concat(e.target.value)});
        }
        this.update();

    };

    update=()=>{
        if (this.state.job_id.length + 1  === this.state.limit){
            this.setState({disable:!this.state.disable})
        }
    };
    removeJob=(e)=>{
        let job_id = this.state.job_id;
        let index = job_id.indexOf(e.target.value);
        job_id.splice(index,1);
        this.setState({job_id: job_id});
        this.update();
    };

    submitHandler=()=>{
        this.setState({loading:!this.state.loading});
        PostData('/api/employer/plan/add_job', this.state).then((result)=>{
            this.setState({loading:!this.state.loading});
            if (result.status === 1)
            {
                alert(result.message);
                window.location.reload();
                // console.log(result)
            }
        })
    };


    render() {
        const {error, preloader, jobs} = this.state;
        if (error) {
            return <div>Error</div>;
        } else if (!preloader) {
            return (
                <div id="loader-wrapper">
                    <div id="loader"/>
                    <div className="loader-section section-left"/>
                    <div className="loader-section section-right"/>
                </div>

            );
        } else {
            const loading= this.state.loading;
            return (
                <div>
                    <EmployerHeader/>
                    <section>
                        <div className="block no-padding">
                            <div className="container">
                                <div className="row no-gape">
                                    <Side/>
                                    <div className="col-lg-9 column">
                                        <div className="padding-left">
                                            <div className="manage-jobs-sec">
                                                <h3>Add Job to Package #{this.state.id}</h3>
                                                {/*<div className="extra-job-info">
                                                <span><i className="la la-clock-o"/><strong>{jobs.length}</strong> Job Posted</span>
                                                <span><i className="la la-file-text"/><strong>20</strong> Application</span>
                                                <span><i className="la la-users"/><strong>18</strong> Active Jobs</span>
                                            </div>*/}
                                                {/*<div className="">*/}
                                                <table>
                                                    <thead>
                                                    <tr>
                                                        <td>Title</td>
                                                        <td>Action</td>
                                                    </tr>
                                                    </thead>
                                                    <tbody>
                                                    {jobs.map(job => (
                                                        <tr>

                                                            <td>
                                                                <div className="table-list-title">
                                                                    <h3><a href="" title="">{job.name}</a></h3>
                                                                </div>
                                                            </td>
                                                            <td>
                                                                {this.state.job_id.includes("" + job.id + "") ?
                                                                    <input type="checkbox" checked onChange={this.removeJob} value={job.id} className="w3-check"/> :
                                                                    <input type="checkbox" disabled={this.state.disable} onChange={this.addJob} value={job.id} className="w3-check"/>
                                                                }
                                                            </td>
                                                        </tr>
                                                    ))}
                                                    </tbody>
                                                </table>
                                                <button type="submit" onClick={this.submitHandler} disabled={loading}>{loading?<i className="la la-spinner la-spin"/>:""} Add Jobs</button>

                                                {/*</div>*/}
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </section>
                </div>
            );
        }
    }
}

export default AddJob;


