import React from 'react';
import {Link} from "react-router-dom";

class Side extends React.Component {
    render() {
        return (
            <aside className="col-lg-3 column border-right">
                <div className="widget">
                    <div className="tree_widget-sec">
                        <ul>
                            <li className="inner-child">
                                <Link to="/profile"><i className="la la-file-text"/>Profile</Link>
                                <ul>
                                    <li><Link to="profile/edit">Edit Profile</Link></li>
                                </ul>
                            </li>
                            <li className="">
                                <Link to="/create_resume"><i className="la la-briefcase"/>My Resume</Link>
                            </li>
                            <li className="inner-child">
                                <Link to="/applied"><i className="la la-flash"/>Applied Jobs</Link>
                            </li>
                            <li className="">
                                <Link to="/change_password"><i className="la la-lock"/>Change Password</Link>
                            </li>
                            <li><a href="" title=""><i className="la la-unlink"/>Logout</a></li>
                        </ul>
                    </div>
                </div>
            </aside>

        );
    }
}

export default Side;


