import React from 'react';
import {Link} from "react-router-dom";

class Resume extends React.Component {
    render() {
        return (
            <div>
                <section>
                    <div className="block double-gap-top double-gap-bottom">
                        <div data-velocity="-.1" style={{background: "url('../../images/slider2.jpg') repeat scroll 50% 422.28px transparent"}} className="parallax scrolly-invisible layer color"></div>
                        <div className="container">
                            <div className="row">
                                <div className="col-lg-12">
                                    <div className="simple-text-block">
                                        <h3>Make a Difference with Your Online Resume!</h3>
                                        <span>Your resume in minutes with DevJobs resume assistant is ready!</span>
                                        <Link to="/signup">Create an Account</Link>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </section>
            </div>
        )
    }
}

export default Resume;


