import React from 'react';
import CandidateHeader from "./layouts/header";
import Side from "./layouts/siderbar";
import {GetData} from "../services/getData";

class Applied extends React.Component {
    constructor(props){
        super(props);
        this.state = {
            jobs:[]
        };
    };

    componentDidMount(){
        GetData('/candidate/applied').then((result)=>{
            if(result.status===1){
                this.setState({jobs:result.data});
                console.log(result);
            }
            else {
                alert(result.error)
            }
        })
    }


    render() {
        const jobs= this.state.jobs;
        return (
            <div>
                <CandidateHeader/>
                <section>
                    <div className="block no-padding">
                        <div className="container">
                            <div className="row no-gape">
                                <Side/>
                                <div className="col-lg-9 column">
                                    <div className="padding-left">
                                        <div className="manage-jobs-sec">
                                            <h3>Recently Applied Jobs</h3>
                                            <table>
                                                <thead>
                                                <tr>
                                                    <td>Job</td>
                                                    <td>Status</td>
                                                    <td>Date</td>
                                                    {/*<td/>*/}
                                                </tr>
                                                </thead>
                                                <tbody>
                                                {jobs.length? jobs.map(job =>(
                                                    <tr>
                                                        <td>
                                                            <div className="table-list-title">
                                                                <h3><a href="" title="">{job.job}</a></h3>
                                                            </div>
                                                        </td>
                                                        <td>
                                                            <span className="applied-field">{job.status}</span>
                                                        </td>
                                                        <td>
                                                            <span>{job.created_at}</span><br />
                                                        </td>
                                                        {/*<td>
                                                            <ul className="action_job">
                                                                <li><span>View Job</span><a href="" title=""><i className="la la-eye"></i></a></li>
                                                                <li><span>Edit</span><a href="" title=""><i className="la la-pencil"/></a></li>
                                                                <li><span>Delete</span><a href="" title=""><i className="la la-trash-o"/></a></li>
                                                            </ul>
                                                        </td>*/}
                                                    </tr>
                                                )):
                                                <tr>
                                                    <td colSpan="4" className="w3-center">
                                                        No Recent Application!
                                                    </td>
                                                </tr>
                                                }
                                                </tbody>
                                            </table>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </section>
            </div>
        );
    }
}

export default Applied


