import React from 'react';
import {GetData} from "../services/getData";
import EmployerHeader from "./layouts/header";
import Side from "./layouts/siderbar";
import {Link} from "react-router-dom";

class MyPackage extends React.Component {
    constructor(props){
        super(props);
        this.state = {
            plans:[],
            preloader: false,
        };
    };

    componentDidMount(){

        GetData("/employer/plan").then((result)=>{
            if(result.status===1){
                this.setState({
                    plans:result.data,
                    preloader: true,
                })

            }
            else {
                console.log(result)
            }
            console.log(result)
        });
    }

    render() {
        const {error, preloader, plans} = this.state;
        if (error) {
            return <div>Error</div>;
        } else if (!preloader) {
            return (
                <div id="loader-wrapper">
                    <div id="loader"/>
                    <div className="loader-section section-left"/>
                    <div className="loader-section section-right"/>
                </div>

            );
        } else {
            return (
                <div>
                    <EmployerHeader/>
                    <section>
                        <div className="block no-padding">
                            <div className="container">
                                <div className="row no-gape">
                                    <Side/>
                                    <div className="col-lg-9 column">
                                        <div className="padding-left">
                                            <div className="manage-jobs-sec">
                                                <h3>My Package</h3>
                                                <Link to="/package"><i className="la la-plus"/> Buy Plan</Link>
                                                <table>
                                                    <thead>
                                                    <tr>
                                                        <td>id</td>
                                                        <td>Plan</td>
                                                        <td>Limit</td>
                                                        <td>Jobs</td>
                                                        <td>Expiry date</td>
                                                        <td>Action</td>
                                                    </tr>
                                                    </thead>
                                                    <tbody>
                                                    {plans.map(plan => (
                                                        <tr>
                                                            <td>
                                                                <div className="table-list-title">
                                                                    <h3>{plan.id}</h3>
                                                                </div>
                                                            </td>
                                                            <td>
                                                                <span className="applied-field">{plan.name}</span>
                                                            </td>
                                                            <td>
                                                                <span>{plan.value}</span><br/>
                                                            </td>
                                                            <td>
                                                                <span>{plan.jobs}</span><br/>
                                                            </td>
                                                            <td>
                                                                <span className="status active">{plan.validity}</span>
                                                            </td>
                                                            <td>
                                                                <ul className="action_job">
                                                                    <li><span>Add Jobs</span><Link to={"/dashboard/package/"+plan.id+"/add"}><i className="la la-plus"/></Link></li>
                                                                    {/*<li><span>Delete</span><a href="" title=""><i className="la la-trash-o"/></a></li>*/}
                                                                </ul>
                                                            </td>
                                                        </tr>
                                                    ))}
                                                    </tbody>
                                                </table>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </section>
                </div>
            );
        }
    }
}

export default MyPackage;


