import React from 'react';
import Side from "./layouts/siderbar";
import EmployerHeader from "./layouts/header";
import {GetData} from "../services/getData";
import {FetchPost} from "../services/fectchPost";
import {PostData} from "../services/PostData";
import {Link} from "react-router-dom";

class Applicants extends React.Component {
    constructor(props){
        super(props);
        this.state = {
            preloader: false,
            applicants:[],
            inviteId:0,
            declineId:0,

        };
    };

    componentDidMount(){
        GetData("/employer/applicants").then((result)=>{
            if(result.status===1){
                this.setState({applicants:result.data})
            }
            else {
                alert(result.error)
            }
            this.setState({preloader: true});
            // console.log(result.data)
        });
    }
    Update=()=>{
        GetData("/employer/applicants").then((result)=>{
            if(result.status===1){
                this.setState({applicants:result.data})
            }
            else {
                alert(result.error)
            }
            this.setState({preloader: true});
            // console.log(result.data)
        });
    };


    declineHandler=(e)=>{
        this.setState({declineId:e.target.id});
        FetchPost('/api/jobs/applicant/'+e.target.id+'/discard').then((result)=>{
            if (result.status===1){
                this.Update();
                alert(result.message);
                this.setState({declineId:""});
            }

        })
    };
    render() {
        const {error, preloader,applicants, declineId, inviteId} = this.state;
        if (error) {
            return <div>Error</div>;
        } else if (!preloader) {
            return (
                <div id="loader-wrapper">
                    <div id="loader"/>
                    <div className="loader-section section-left"/>
                    <div className="loader-section section-right"/>
                </div>
            );
        } else {
            return (
                <div>
                    <EmployerHeader/>
                    <section>
                        <div className="block no-padding">
                            <div className="container">
                                <div className="row no-gape">
                                    <Side/>
                                    <div className="col-lg-9 column">
                                        <div className="padding-left">
                                            <div className="manage-jobs-sec">
                                                <h3>Manage applicants</h3>
                                                <table>
                                                    <thead>
                                                    <tr>
                                                        <td>Applicant</td>
                                                        <td>Job</td>
                                                        <td>Date</td>
                                                        <td>Status</td>
                                                        <td>Action</td>
                                                    </tr>
                                                    </thead>
                                                    <tbody>
                                                    {applicants.map(applicant => (
                                                        <tr>
                                                            <td>
                                                                <div className="table-list-title">
                                                                    <h3><Link to={applicant.username+"/profile"} target="_blank">{applicant.name}</Link></h3>
                                                                </div>
                                                            </td>
                                                            <td>
                                                                <span className="applied-field"><Link to={"/job/"+applicant.link} target="_blank">{applicant.job}</Link></span>
                                                            </td>
                                                            <td>
                                                                <span>{applicant.date}</span><br/>
                                                            </td>
                                                            <td>
                                                                <span
                                                                    className="status active">{applicant.status === 0 ? "Pending" : applicant.status === 1 ? "Interview" : "Declined"}</span>
                                                            </td>
                                                            <td>
                                                                <ul className="action_job">
                                                                    <li><span>Invite</span><Link to={"/applicant/interview/"+applicant.id}><i id={applicant.id} className={inviteId===(""+applicant.id+"")?"la la-spinner la-spin":"la la-envelope"}/></Link></li>
                                                                    <li><span>Decline</span><a><i onClick={this.declineHandler} id={applicant.id} className={declineId===(""+applicant.id+"")?"la la-spinner la-spin":"la la-times"}/></a></li>
                                                                </ul>
                                                            </td>
                                                        </tr>
                                                    ))}
                                                    </tbody>
                                                </table>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </section>
                </div>
            );
        }
    }
}
class Modal extends React.Component{

    constructor(props){
        super(props);
        this.state = {
            loading:"",
        };
    };

    render(){
        return(
            <div id="id01" className="w3-modal" style={{display:'block'}}>
                <div className="w3-modal-content" style={{maxWidth:550}}>
                    <div className="w3-container w3-padding-large">
                        <h3>Invite {this.props.name}</h3>
                        {/*<span onClick={document.getElementById('id01').style.display='none'} className="w3-button w3-display-topright">&times;</span>*/}
                        <div className="w3-margin">
                            <form action="">
                                <div className="pf-field">
                                    <span className="pf-title">Address or Link</span>
                                    <input type="text"/>
                                </div>
                                <div className="pf-field">
                                    <span className="pf-title">Date</span>
                                    <input type="date"/>
                                </div>
                                <div className="pf-field">
                                    <span className="pf-title">Time</span>
                                    <input type="time"/>
                                </div>
                            </form>

                        </div>
                    </div>
                </div>
            </div>
        )
    }


}

export default Applicants;


