import React from 'react';
import CandidateHeader from "./layouts/header";
import Side from "./layouts/siderbar";
import {Link} from "react-router-dom";

class CandidateDashboard extends React.Component {
    render() {
        return (
            <div>
                <CandidateHeader/>
                <section>
                    <div className="block no-padding">
                        <div className="container">
                            <div className="row no-gape">
                                <Side/>
                                <div className="col-lg-9 column">
                                    <div className="padding-left">
                                        <div className="manage-jobs-sec">
                                            <h3>Dashboard</h3>
                                            <div className="cat-sec w3-margin-bottom">
                                                <div className="row no-gape">
                                                    <div className="col-lg-4 col-md-4 col-sm-12">
                                                        <div className="p-category">
                                                            <Link to="/applied">
                                                                <i className="la la-briefcase"/>
                                                                <span>Applied Job</span>
                                                            </Link>
                                                        </div>
                                                    </div>
                                                    <div className="col-lg-4 col-md-4 col-sm-12">
                                                        <div className="p-category">
                                                            <Link to="/profile">
                                                                <i className="la la-file"/>
                                                                <span>My Profile</span>
                                                            </Link>
                                                        </div>
                                                    </div>
                                                    {/*<div className="col-lg-4 col-md-4 col-sm-12">
                                                        <div className="p-category">
                                                            <Link to="/dashboard/jobs">
                                                                <i className="la la-eye "></i>
                                                                <span>Interview</span>
                                                                <p>22 View Statistic</p>
                                                            </Link>
                                                        </div>
                                                    </div>*/}
                                                </div>
                                            </div>
                                            <div className="cat-sec">
                                                <div className="row no-gape">
                                                    {/*<div className="col-lg-4 col-md-4 col-sm-12">
                                                        <div className="p-category">
                                                            <a href="" title="">
                                                                <i className="la la-file"></i>
                                                                <span>My Profile</span>
                                                                <p>View Profile</p>
                                                            </a>
                                                        </div>
                                                    </div>*/}
                                                </div>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </section>
            </div>
        );
    }
}

export default CandidateDashboard;


