import React from 'react';
import Side from "./layouts/siderbar";
import EmployerHeader from "./layouts/header";
import {Link} from "react-router-dom";
import '../../styles/bootstrap-grid.css';

class EmployerDashboard extends React.Component {
    render() {
        return (
            <div>
                <EmployerHeader/>
                <section>
                    <div className="block no-padding">
                        <div className="container">
                            <div className="row no-gape">
                                <Side/>
                                <div className="col-lg-9 column">
                                    <div className="padding-left">
                                        <div className="manage-jobs-sec w3-margin-bottom">
                                            <h3>Dashboard</h3>
                                            <div className="cat-sec">
                                                <div className="row no-gape">
                                                    <div className="col-lg-4 col-md-4 col-sm-12">
                                                        <div className="p-category">
                                                            <Link to="/dashboard/jobs">
                                                                <i className="la la-briefcase"/>
                                                                <span>Posted Job</span>
                                                            </Link>
                                                        </div>
                                                    </div>
                                                    <div className="col-lg-4 col-md-4 col-sm-12">
                                                        <div className="p-category view-resume-list">
                                                            <Link to="/applicants">
                                                                <i className="la la-users"/>
                                                                <span>Applicants</span>
                                                            </Link>
                                                        </div>
                                                    </div>
                                                    <div className="col-lg-4 col-md-4 col-sm-12">
                                                        <div className="p-category">
                                                            <Link to="/interview">
                                                                <i className="la la-eye "/>
                                                                <span>Interview</span>
                                                            </Link>
                                                        </div>
                                                    </div>
                                                </div>
                                            </div>
                                            <div className="cat-sec">
                                                <div className="row no-gape">
                                                    <div className="col-lg-4 col-md-4 col-sm-12">
                                                        <div className="p-category">
                                                            <Link to="/payment">
                                                                <i className="la la-credit-card"/>
                                                                <span>Payments</span>
                                                            </Link>
                                                        </div>
                                                    </div>
                                                    <div className="col-lg-4 col-md-4 col-sm-12">
                                                        <div className="p-category">
                                                            <Link to="/profile">
                                                                <i className="la la-file"/>
                                                                <span>My Profile</span>
                                                            </Link>
                                                        </div>
                                                    </div>
                                                    <div className="col-lg-4 col-md-4 col-sm-12">
                                                        <div className="p-category">
                                                            <Link to="/dashboard/package">
                                                                <i className="la la-gift"/>
                                                                <span>My Package</span>
                                                            </Link>
                                                        </div>
                                                    </div>
                                                </div>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </section>
            </div>

        );
    }
}

export default EmployerDashboard;



