import React from 'react';
import EmployerHeader from "./layouts/header";
import Side from "./layouts/siderbar";
import {GetData} from "../services/getData";


class Interview extends React.Component {

    constructor(props){
        super(props);
        this.state = {
            applicants:[],
            preloader: false,
        };
    };

    componentDidMount(){
        GetData("/employer/applicants/interview").then((result)=>{
            if(result.status===1){
                this.setState({applicants:result.data,preloader: true,})

            }
            else {
                alert(result.error)
            }
            console.log(result.data)
        });
    }

    render() {
        const {error, preloader, applicants} = this.state;
        if (error) {
            return <div>Error</div>;
        } else if (!preloader) {
            return (
                <div id="loader-wrapper">
                    <div id="loader"/>
                    <div className="loader-section section-left"/>
                    <div className="loader-section section-right"/>
                </div>

            );
        } else {
            return (
                <div>
                    <EmployerHeader/>
                    <section>
                        <div className="block no-padding">
                            <div className="container">
                                <div className="row no-gape">
                                    <Side/>
                                    <div className="col-lg-9 column">
                                        <div className="padding-left">
                                            <div className="manage-jobs-sec">
                                                <h3>Interview</h3>
                                                <table>
                                                    <thead>
                                                    <tr>
                                                        <td>Name</td>
                                                        <td>Job</td>
                                                        <td>Date</td>
                                                        <td>Time</td>
                                                        <td>Action</td>
                                                    </tr>
                                                    </thead>
                                                    <tbody>
                                                    {applicants.length > 0 ? applicants.map(applicant => (
                                                            <tr>
                                                                <td>
                                                                    <div className="table-list-title">
                                                                        <h3><a href="" title="">{applicant.name}</a></h3>
                                                                    </div>
                                                                </td>
                                                                <td>
                                                                    <span className="applied-field">{applicant.job}</span>
                                                                </td>
                                                                <td>
                                                                    <span>{applicant.date}</span><br/>
                                                                </td>
                                                                <td>
                                                                    <span className="status active">{applicant.time}</span>
                                                                </td>
                                                                <td>
                                                                    <ul className="action_job">
                                                                        {/*<li><span>View applicant</span><a href="" title=""><i className="la la-eye"></i></a></li>*/}
                                                                        <li><span>Edit</span><a href="" title=""><i
                                                                            className="la la-pencil"/></a></li>
                                                                        <li><span>Delete</span><a href="" title=""><i
                                                                            className="la la-trash-o"/></a></li>
                                                                    </ul>
                                                                </td>
                                                            </tr>
                                                        )) :

                                                        <tr>
                                                            <td colSpan="5" className="w3-center">No Interviews!</td>
                                                        </tr>}
                                                    </tbody>
                                                </table>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </section>
                </div>
            );
        }
    }
}

export default Interview;


