import React from 'react';

class HIW extends React.Component {
    render() {
        return (
            <section>
                <div className="block">
                    <div className="container">
                        <div className="row">
                            <div className="col-lg-12">
                                <div className="heading">
                                    <h2>How It Works</h2>
                                </div>
                                <div className="how-to-sec">
                                    <div className="how-to">
                                        <span className="how-icon"><i className="la la-user"/></span>
                                        <h3>Register an account</h3>
                                        <p>Post a job to tell us about your project. We'll quickly match you with the
                                            right freelancers.</p>
                                    </div>
                                    <div className="how-to">
                                        <span className="how-icon"><i className="la la-file-archive-o"/></span>
                                        <h3>Create your profile and resume</h3>
                                        <p>Browse profiles, reviews, and proposals then interview top candidates. </p>
                                    </div>
                                    <div className="how-to">
                                        <span className="how-icon"><i className="la la-list"/></span>
                                        <h3>Apply for job</h3>
                                        <p>Use the platform to chat, share files, and collaborate from your
                                            desktop or on the go.</p>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </section>
        );
    }
}

export default HIW;


