import React from 'react';
import Search from "./search";
import RecentJob from "./recentJob";
import HIW from "./hiw";
import Resume from "./resume";
import Hire from "./hiring";

class Home extends React.Component {

    render() {
        return (
            <div>
                <Search />
                <RecentJob />
                <HIW/>
                <Resume/>
                <Hire/>
            </div>
        );
    }
}

export default Home;


