import React from 'react';
import {Redirect} from "react-router-dom";

function Logout() {
    localStorage.removeItem('auth');
    localStorage.removeItem('user');
    localStorage.removeItem('expired');
    window.location.reload();
    // return (
    //     <Redirect to="/login"/>
    // )
}

export default Logout;



