import React from 'react';
import NavLink from "react-router-dom/es/NavLink";
import {Link} from "react-router-dom";
class Nav extends React.Component {
    constructor(props){
        super(props);
        this.state = {
            display:false
        };
    };

    toggleHandle=()=>{
        this.setState({display:!this.state.display})
    };


    render() {
        if (localStorage.getItem('auth')){
            return(
                <div>
                    <div className="responsive-header">
                        <div className="responsive-menubar">
                            <div className="res-logo"><Link to="/"><img src="../../images/logo.png" alt="" /></Link></div>
                            <div className="menu-resaction">
                                <div onClick={this.toggleHandle} className="">
                                    <img src="../../images/icon.png" alt="" />
                                </div>
                                {/*<div className="">*/}
                                    {/*<img src="../../images/icon.png" alt="" />*/}
                                {/*</div>*/}
                            </div>
                        </div>
                        {this.state.display?
                            <div style={{display:"block"}} className="responsive-opensec">
                                <div className="btn-extars">
                                    {this.props.user===2?<Link to="/post_job" className="post-job-btn"><i className="la la-plus"/>Post Jobs</Link>:""}
                                    <ul className="account-btns">
                                        {this.props.auth?
                                            <li className="signin-popup"><Link to="/logout"><i className="la la-external-link-square"/> Logout</Link></li>:
                                            <li className="signin-popup"><Link to="/login"><i className="la la-external-link-square"/> Login</Link></li>
                                        }
                                        {!this.props.auth?
                                            <li className="signup-popup"><Link to="/signup"><i className="la la-key"/> SignUp</Link></li>:""
                                        }
                                    </ul>
                                </div>

                                <div className="responsivemenu">
                                    <ul>
                                        <li className="">
                                            <Link onClick={this.toggleHandle} to="/">Home</Link>
                                        </li>

                                        <li className="menu-item-has-children">
                                            <Link onClick={this.toggleHandle} to="/profile">Profile</Link>
                                            <ul>
                                                <li><Link to="/profile/edit"> Edit Profile</Link></li>
                                            </ul>
                                        </li>

                                        <li className="">
                                            <Link onClick={this.toggleHandle} to="/dashboard">Dashboard</Link>
                                        </li>

                                        <li className="">
                                            <Link onClick={this.toggleHandle} to="/jobs">Jobs</Link>
                                        </li>
                                    </ul>
                                </div>
                            </div>
                            :""
                        }
                    </div>

                    <header className="stick-top">
                        <div className="menu-sec">
                            <div className="container">
                                <div className="logo">
                                    <Link to="/"> <img src="../../images/logo.png" alt="" /></Link>
                                </div>
                                <div className="my-profiles-sec">
                                    <span><img src={"http://api.devjobs.ng/dp/"+(this.props.username)+".png"} alt="" style={{width:50, height:50}} /> {this.props.username}</span>
                                    {/*<span><img src="http://www.thecellartrust.org/wp-content/uploads/2017/04/Trustees.jpg" alt="" style={{width:50, height:50}} /> {this.props.username}</span>*/}
                                </div>
                                {/*<div className="wishlist-dropsec">
                                    <span><i className="la la-heart"></i><strong>3</strong></span>
                                </div>*/}
                                <nav>
                                    <ul>
                                        <li className="">
                                            <Link to="/">Home</Link>
                                        </li>
                                        <li className="menu-item-has-children">
                                            <Link to="/profile">Profile</Link>
                                            <ul>
                                                <li><Link to="/profile/edit"> Edit Profile</Link></li>
                                            </ul>
                                        </li>
                                        <li className="">
                                            <Link to="/dashboard">Dashboard</Link>
                                        </li>
                                        <li className="">
                                            <Link to="/jobs">Jobs</Link>
                                        </li>
                                        <li className="">
                                            {this.props.role === 2? <Link to="/post_job" className="post-job-btn signup-popup"><i className="la la-plus"/>Post Jobs</Link>: ""}
                                        </li>
                                        <li className="">
                                            <Link to="/logout"><i className="la la-external-link-square"/> Log Out</Link>
                                        </li>
                                    </ul>
                                </nav>
                            </div>
                        </div>
                    </header>

                </div>
            )
        }else{
            return (
                <div>
                    <div className="responsive-header">
                        <div className="responsive-menubar">
                            <div className="res-logo"><Link to="/"><img src="../../images/logo.png" alt="" /></Link></div>
                            <div className="menu-resaction">
                                <ul className="account-btns">
                                    <li className="signin-popup"><Link to="/login"><i className="la la-external-link-square"/> Login</Link></li>
                                    <li className="signup-popup"><Link to="/signup"><i className="la la-key"/> SignUp</Link></li>
                                </ul>
                            </div>
                        </div>
                        <div className="responsive-opensec">
                            <div className="btn-extars">
                                <ul className="account-btns">
                                        <li className="signin-popup"><Link to="/login"><i className="la la-external-link-square"/> Login</Link></li>
                                        <li className="signup-popup"><Link to="/signup"><i className="la la-key"/> SignUp</Link></li>
                                </ul>
                            </div>

                        </div>
                    </div>

                    <header className="stick-top style2">
                        <div className="menu-sec">
                            <div className="container fluid">
                                <div className="logo">
                                    <Link to="/" exact activeStyle={{color :'green'}}> <img src="../../images/logo.png" alt="" /></Link>
                                    {/*<a href="index.html" title=""><img src="images/logo.png" alt="" /></a>*/}
                                </div>{/*<!-- Logo -->*/}
                                <div className="btn-extars">
                                    <Link to="/post_job" className="post-job-btn"><i className="la la-plus"/>Post Jobs</Link>
                                    <ul className="account-btns">
                                        <li className="signup-popup"><Link to="/signup"><i className="la la-key"/> Sign Up</Link></li>
                                        <li className="signin-popup"><Link to="/login"><i className="la la-external-link-square"/> Login</Link></li>
                                    </ul>
                                </div>{/*<!-- Btn Extras -->*/}
                            </div>
                        </div>
                    </header>
                </div>
            );
        }

    }
}

export default Nav;


