import React from 'react';
import EmployerHeader from "./layouts/header";
import {GetData} from "../services/getData";
import {Redirect} from "react-router-dom";

class EmployerProfile extends React.Component {

    constructor(props){
        super(props);
        this.state = {
            auth: false,
            username:"",
            role:"",
            href:"",
            preloader: false,
            social:[],
        };
    };

    componentDidMount() {
        if (localStorage.getItem('auth') === null) {
            // const user= localStorage.getItem('user');
            this.setState({auth: false})
        }
        else {
            const user= JSON.parse(localStorage.getItem('user'));
            this.setState({auth: true, username: user.username, role: user.role,});

            GetData("/employer/profile").then((result)=>{
                if (result.status===1){
                    this.setState(
                        result.data
                    );
                    this.setState({preloader: true,})
                }else{
                    this.setState({error: true,});
                }
                console.log(result);
            })
        }
    }

    render() {
        const {error, preloader,} = this.state;
        if (error) {
            return <Redirect to="/profile/edit"/>
        } else if (!preloader) {
            return (
                <div id="loader-wrapper">
                    <div id="loader"/>
                    <div className="loader-section section-left"/>
                    <div className="loader-section section-right"/>
                </div>

            );
        } else {
            return (
                <div>
                    <EmployerHeader/>
                    <section>
                        <div className="block">
                            <div className="container">
                                <div className="row">
                                    <div className="col-lg-8 column">
                                        <div className="job-single-sec style3">
                                            <div className="job-wide-devider">
                                                <div className="job-overview divide">
                                                    <h3>Company Information</h3>
                                                    <ul>
                                                        <li><i className="la la-map"/><h3>Location</h3>
                                                            <span>{this.state.state + ", " + this.state.country}</span>
                                                        </li>
                                                        <li><i className="la la-bars"/><h3>Industry</h3>
                                                            <span>{this.state.sector}</span></li>
                                                    </ul>

                                                </div>
                                                {/*<!-- Job Overview -->*/}
                                                <div className="job-details">
                                                    <h3>About</h3>
                                                    <p>{this.state.about}</p>
                                                </div>
                                                {/*<div className="recent-jobs">
                                                <h3>Jobs from Business Network</h3>
                                                <div className="job-list-modern">
                                                    <div className="job-listings-sec no-border">
                                                        <div className="job-listing wtabs noimg">
                                                            <div className="job-title-sec">
                                                                <h3><a href="" title="">Web Designer / Developer</a></h3>
                                                                <span>Massimo Artemisis</span>
                                                                <div className="job-lctn"><i className="la la-map-marker"></i>Sacramento, California</div>
                                                            </div>
                                                            <div className="job-style-bx">
                                                                <span className="job-is ft">Full time</span>
                                                                <span className="fav-job"><i className="la la-heart-o"></i></span>
                                                                <i>5 months ago</i>
                                                            </div>
                                                        </div>
                                                        <div className="job-listing wtabs noimg">
                                                            <div className="job-title-sec">
                                                                <h3><a href="" title="">C Developer (Senior) C .Net</a></h3>
                                                                <span>Massimo Artemisis</span>
                                                                <div className="job-lctn"><i className="la la-map-marker"></i>Sacramento, California</div>
                                                            </div>
                                                            <div className="job-style-bx">
                                                                <span className="job-is pt ">Part time</span>
                                                                <span className="fav-job"><i className="la la-heart-o"></i></span>
                                                                <i>5 months ago</i>
                                                            </div>
                                                        </div><!-- Job -->
                                                        <div className="job-listing wtabs noimg">
                                                            <div className="job-title-sec">
                                                                <h3><a href="" title="">Regional Sales Manager South</a></h3>
                                                                <span>Massimo Artemisis</span>
                                                                <div className="job-lctn"><i className="la la-map-marker"></i>Sacramento, California</div>
                                                            </div>
                                                            <div className="job-style-bx">
                                                                <span className="job-is ft ">Full time</span>
                                                                <span className="fav-job"><i className="la la-heart-o"></i></span>
                                                                <i>5 months ago</i>
                                                            </div>
                                                        </div><!-- Job -->
                                                        <div className="job-listing wtabs noimg">
                                                            <div className="job-title-sec">
                                                                <h3><a href="" title="">Marketing Dairector</a></h3>
                                                                <span>Massimo Artemisis</span>
                                                                <div className="job-lctn"><i className="la la-map-marker"></i>Sacramento, California</div>
                                                            </div>
                                                            <div className="job-style-bx">
                                                                <span className="job-is ft ">Full time</span>
                                                                <span className="fav-job"><i className="la la-heart-o"></i></span>
                                                                <i>5 months ago</i>
                                                            </div>
                                                        </div><!-- Job -->
                                                    </div>
                                                </div>
                                            </div>*/}
                                            </div>
                                        </div>
                                    </div>
                                    <div className="col-lg-4 column">
                                        <div className="job-single-head style2">
                                            <div className="job-thumb"><img style={{width: 124, height: 124}}
                                                                            src={this.state.image} alt=""/></div>
                                            <div className="job-head-info">
                                                <h4>{this.state.name}</h4>
                                                <span>{this.state.state + ", " + this.state.country}</span>
                                                <p><i className="la la-unlink"/>{this.state.website}</p>
                                            </div>
                                            <div className="share-bar">
                                                <a href={this.state.social.google} target="_blank" title=""
                                                   className="share-google"><i className="la la-google"/></a><a
                                                target="_blank" href={this.state.social.facebook} title=""
                                                className="share-fb"><i className="fa fa-facebook"/></a><a
                                                href={this.state.social.linkedin} target="_blank" title=""
                                                className="share-twitter"><i className="fa fa-linkedin"/></a>
                                            </div>
                                        </div>
                                        {/*<!-- Job Head -->*/}
                                    </div>
                                </div>
                            </div>
                        </div>
                    </section>
                </div>
            );
        }
    }
}

export default EmployerProfile;


