export function FetchPost(type) {
    // let BaseURL = 'http://127.0.0.1:8000';
    let BaseURL = 'http://api.devjobs.ng';
    let auth = localStorage.getItem('auth');
    let token = auth?auth.replace(/['"]+/g, ''):"";
    return new Promise((resolve, reject) =>{


        fetch(BaseURL+type, {
            method: 'POST',
            headers: {
                Accept: 'application/json',
                'Content-Type': 'application/json',
                'Authorization': token
            },
        })
            .then((response) => response.json())
            .then((res) => {
                resolve(res);
            })
            .catch((error) => {
                reject(error);
            });


    });
}


