import React from 'react';
import PaystackButton from 'react-paystack';
import EmployerHeader from "./employer/layouts/header";
import {PostData} from "./services/PostData";
import {FetchGet} from "./services/fetchget";
import {Link} from "react-router-dom";
import '../styles/bootstrap-grid.css';

const user= JSON.parse(localStorage.getItem('user'));

class Package extends React.Component {
    constructor(props){
        super(props);
        this.state = {
            key: "pk_test_a8bd29bb7432d9f2f9e973c85be77fefecf2b1b7",
            email: user.email,  // customer email
            amount: 0,
            jobs:[],
            payment:"",
            success:false,
        };
    };

    callback = (response) => {
        console.log(response);
        if(response.reference){
            FetchGet('https://api.paystack.co/transaction/verify/'+response.reference).then((result)=>{
                this.setState({payment:result});
                if (result.data.status === "success"){
                    console.log(result);
                    this.process();
                }else{
                    this.setState({success:false});
                }
            });
        }
    };

    process(){
        PostData('/api/employer/payment', this.state.payment).then((result)=>{
            if (result.status===1){
                this.setState({
                    message:result.message,
                    success:true
                })
            }
        });
    }

    close = () => {
        console.log("Payment closed");
    };

    getReference = () => {
        //you can put any unique reference implementation code here
        let text = "";
        let possible = "ABCDEFGHIJKLMNOPQRSTUVWXYZabcdefghijklmnopqrstuvwxyz0123456789-.=";

        for( let i=0; i < 15; i++ )
            text += possible.charAt(Math.floor(Math.random() * possible.length));

        return text;
    };
    render() {
        return (
            <div>
                <EmployerHeader/>
                <section>
                    <div className="block">
                        <div className="container">
                            <div className="row">
                                <div className="col-lg-12">
                                    <div className="heading">
                                        <h2>Buy Our Plans And Packeges</h2>
                                        <span>One of our jobs has some kind of flexibility option - such as telecommuting, a part-time schedule or a flexible or flextime schedule.</span>
                                    </div>
                                    <div className="plans-sec">
                                        <div className="row">
                                            <div className="col-lg-4">
                                                <div className="pricetable">
                                                    <div className="pricetable-head">
                                                        <h3>Basic Jobs</h3>
                                                        <h2><i>&#8358;</i>14000</h2>
                                                        <span>7 Days</span>
                                                    </div>
                                                    <ul>
                                                        <li>2 job posting</li>
                                                        {/*<li>0 featured job</li>*/}
                                                        <li>Job displayed for 7 days</li>
                                                        <li>Premium Support 24/7</li>
                                                    </ul>

                                                    <div className="w3-center">
                                                        <PaystackButton
                                                            text={"BUY NOW"}
                                                            class="w3-btn navblue w3-padding-large w3-round-xxlarge"
                                                            callback={this.callback}
                                                            close={this.close}
                                                            disabled={false}
                                                            embed={false}
                                                            reference={this.getReference()}
                                                            email={this.state.email}
                                                            amount={1400000}
                                                            paystackkey={this.state.key}
                                                        />
                                                    </div>
                                                </div>
                                            </div>
                                            <div className="col-lg-4">
                                                <div className="pricetable">
                                                    <div className="pricetable-head">
                                                        <h3>Standard Jobs</h3>
                                                        <h2><i>&#8358;</i>32,000</h2>
                                                        <span>15 Days</span>
                                                    </div>
                                                    <ul>
                                                        <li>5 job posting</li>
                                                        {/*<li>12 featured job</li>*/}
                                                        <li>Job displayed for 15 days</li>
                                                        <li>Premium Support 24/7</li>
                                                    </ul>
                                                    <div className="w3-center">
                                                        <PaystackButton
                                                            text={"BUY NOW"}
                                                            class="w3-btn navblue w3-padding-large w3-round-xxlarge"
                                                            callback={this.callback}
                                                            close={this.close}
                                                            disabled={false}
                                                            embed={false}
                                                            reference={this.getReference()}
                                                            email={this.state.email}
                                                            amount={3200000}
                                                            paystackkey={this.state.key}
                                                        />
                                                    </div>
                                                </div>
                                            </div>
                                            <div className="col-lg-4">
                                                <div className="pricetable">
                                                    <div className="pricetable-head">
                                                        <h3>Golden Jobs</h3>
                                                        <h2><i>&#8358;</i>70,000</h2>
                                                        <span>30 Days</span>
                                                    </div>
                                                    <ul>
                                                        <li>12 job posting</li>
                                                        {/*<li>56 featured job</li>*/}
                                                        <li>Job displayed for 30 days</li>
                                                        <li>Premium Support 24/7</li>
                                                    </ul>
                                                    <div className="w3-center">
                                                        <PaystackButton
                                                            text={"BUY NOW"}
                                                            class="w3-btn navblue w3-padding-large w3-round-xxlarge"
                                                            callback={this.callback}
                                                            close={this.close}
                                                            disabled={false}
                                                            embed={false}
                                                            reference={this.getReference()}
                                                            email={this.state.email}
                                                            amount={7000000}
                                                            paystackkey={this.state.key}
                                                        />
                                                    </div>
                                                </div>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </section>
                <div style={this.state.success?{display:'block'}: {display:'none'}} className="w3-modal">
                    <div className="w3-modal-content w3-round-large w3-animate-top" style={{maxWidth:500}}>
                        <div className="w3-container">
                            <div className="w3-center w3-text-green" style={{fontSize:84}}>
                                <i className="la la-check-circle" style={{fontSize:100}}/>
                            </div>
                            <p className="w3-text-green w3-center">{this.state.message}</p>
                            <div className="w3-row-padding w3-center w3-margin-top w3-margin-bottom">
                                <div className="w3-half">
                                    <button onClick={this.onClickAdd} className="w3-btn w3-blue ">Buy Another Package</button>
                                </div>
                                <div className="w3-half">
                                    <Link to={"/dashboard/package"} className="w3-btn w3-green w3-round">Add Jobs to Package</Link>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        )
    }
}

export default Package;


