import React from 'react';
import {PostData} from "../services/PostData";
import {Link} from "react-router-dom";
import axios from 'axios';
// import '../../styles/bootstrap.min.css';

class Verify extends React.Component {
    constructor(props){
        super(props);
        this.state = {
            loading:"",
            code: 0,
            alert:"kindly check your email for the verification code.",
            color:"alert-success",
            success:false,
            display:true,
        };
    };
    componentDidMount(){
        const user= JSON.parse(localStorage.getItem('user'));
        if (user.status===1){
            this.setState({
                success:!this.state.success,
            })
        }
    }
    verifyHandler=(e)=>{
        e.preventDefault();
        this.setState({loading:!this.state.loading});
        PostData('/api/auth/user/activate', this.state).then((result)=>{
            this.setState({loading:!this.state.loading});
            if(result.status === 1){
                let token = localStorage.getItem('auth').replace(/['"]+/g, '');

                // axios.get('http://127.0.0.1:8000/api/user', {
                    axios.get('http://api.devjobs.ng/api/user', {
                    headers: {
                        'Accept': 'application/json',
                        'Content-Type': 'application/json',
                        'Authorization': token,
                    },
                })
                    .then(function (response) {
                        // console.log(response.data)
                        localStorage.setItem('user', JSON.stringify(response.data));
                        window.location.reload()
                    });
                // this.setState({
                //     alert:result.message,
                //     success:!this.state.success,
                // })
            }else{
                this.setState({
                    alert:result.error,
                    color:"alert-danger"
                })
            }
        })
    };
    resendHandler=()=>{
        PostData('/api/auth/resend').then((result)=>{
            this.setState({alert:result.message});
            if(result.status === 1){
                this.setState({
                    alert:result.message,
                })
            }else{
                this.setState({
                    alert:result.error,
                    color:"alert-danger"
                })
            }
        })
    };
    onChange=(e)=>{
        this.setState({code:e.target.value})
    };

    onClick=()=>{
        this.setState({
            display:!this.state.display,
        })
    };

    render() {
        const loading= this.state.loading;
        if(this.state.success){
            return (
                <div className="account-popup-area signin-popup-box" style={{display:"block"}}>
                    <div className="account-popup">
                        <div>
                            <h3 className="w3-text-green">Account activated successfully</h3>
                        </div>
                        <div>
                            <i className="la la-check-circle w3-text-green" style={{fontSize:200}}/>
                        </div>
                        <Link to="/profile/edit" className="w3-btn w3-block w3-round-large w3-blue w3-padding-large">Continue To Profile</Link>
                    </div>
                </div>
            );
        }else{
            return (
                <div className="account-popup-area signin-popup-box" style={{display:"block"}}>
                    <div className="account-popup">
                        {this.state.display?
                            <div id="alert" className={"alert"+" "+this.state.color}>
                                <a className="close" data-dismiss="alert" aria-label="close" onClick={this.onClick}>&times;</a>
                                {this.state.alert}
                            </div>:""}
                        <h3>Verify Your Email</h3>
                        <form onSubmit={this.verifyHandler}>
                            <div className="cfield">
                                <input type="text" name="code" onChange={this.onChange}/>
                                <i className="la la-key"/>
                            </div>
                            <button disabled={loading}>{loading?<i className="la la-spinner la-spin"/>:"Verify"}</button>
                        </form>
                        <div className="w3-margin-top w3-right">
                            <em className="w3-margin">Click <a onClick={this.resendHandler} className="w3-text-red">Here</a> to resend Confirmation code</em>
                        </div>
                    </div>
                </div>
            );
        }
    }
}

export default Verify;


