import React from 'react';
import {PostData} from "../services/PostData";
import axios from "axios/index";
import {Redirect} from "react-router-dom";

class DirectVerify extends React.Component {

    constructor(props){
        super(props);
        this.state = {
            code: 0,
            success:false,
        };
    };

    componentDidMount(){
        const { code } = this.props.match.params;

        let form={
            code:code
        };

        this.setState({code: code});
        this.verifyHandler(form);
    }

    verifyHandler=(form)=>{
        // e.preventDefault();
        PostData('/api/auth/user/activate', form).then((result)=>{
            if(result.status === 1){
                let token = localStorage.getItem('auth').replace(/['"]+/g, '');

                axios.get('http://127.0.0.1:8000/api/user', {
                    // axios.get('http://api.devjobs.ng/api/user', {
                    headers: {
                        'Accept': 'application/json',
                        'Content-Type': 'application/json',
                        'Authorization': token,
                    },
                })
                    .then(function (response) {
                        // console.log(response.data)
                        localStorage.setItem('user', JSON.stringify(response.data));
                        window.location.reload()
                    });
                this.setState({
                    alert:result.message,
                    success:!this.state.success,
                })
            }else{
                this.setState({
                    alert:result.error,
                    color:"alert-danger"
                })
            }
        })
    };

    render() {
        if (this.state.success) {
            return  <Redirect to="/activate"/>
        }else{
            return (
                <div id="loader-wrapper">
                    <div id="loader"/>
                    <div className="loader-section section-left"/>
                    <div className="loader-section section-right"/>
                </div>
            )
        }

    }
}

export default DirectVerify;


