import React from 'react';
import JobHeader from "./layouts/header";
import {Link} from "react-router-dom";
import Pagination from "react-js-pagination";

class Jobs extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            error: null,
            jobs: [],
            activePage: 1,
            preloader: false,
        };
    }

    handlePageChange=(pageNumber)=> {
        // console.log(`active page is ${pageNumber}`);
        this.setState({activePage: pageNumber, preloader: !this.state.preloader,});
        // fetch("http://127.0.0.1:8000/api/jobs?page="+pageNumber)
        fetch("http://api.devjobs.ng/api/jobs?page="+pageNumber)
            .then(res => res.json())
            .then(
                (result) => {
                    this.setState({
                        jobs: result.data,
                        preloader: !this.state.preloader,
                    });
                    this.setState(result.pagination);
                    console.log(result)
                },
                (error) => {
                    this.setState({
                        isLoaded: true,
                        error
                    });
                }
            );
    };

    componentDidMount() {
        // fetch("http://127.0.0.1:8000/api/jobs")
        fetch("http://api.devjobs.ng/api/jobs")
            .then(res => res.json())
            .then(
                (result) => {
                    this.setState({
                        jobs: result.data,
                        preloader: true,
                    });
                    this.setState(result.pagination);
                    console.log(result)
                },
                (error) => {
                    this.setState({
                        isLoaded: true,
                        error
                    });
                }
            );
    }

    render() {
        // const { error, isLoaded, jobs } = this.state;
        const {error, preloader, jobs } = this.state;
        if (error) {
            return (
                <div>
                    <JobHeader/>
                    <section>
                        <div className="block">
                            <div className="container">
                                <div className="row">
                                    <div className="col-lg-12">
                                        <div className="job-grid-sec alert alert-danger">
                                            <h3 className="w3-text-red w3-center">Network failure!, please check your connection</h3>
                                        </div>
                                        <div className=" w3-right">
                                            <Pagination
                                                activePage={this.state.activePage}
                                                itemsCountPerPage={10}
                                                totalItemsCount={this.state.total}
                                                pageRangeDisplayed={5}
                                                onChange={this.handlePageChange}
                                            />
                                        </div>{/*<!-- Pagination -->*/}
                                    </div>
                                </div>
                            </div>
                        </div>

                    </section>
                </div>
            )
        } else if (!preloader) {
            return (
                <div id="loader-wrapper">
                    <div id="loader"/>
                    <div className="loader-section section-left"/>
                    <div className="loader-section section-right"/>
                </div>

            );
        }else {
            return (
                <div>
                <JobHeader/>
                <section>
                    <div className="block">
                        <div className="container">
                            <div className="row">
                                <div className="col-lg-12">
                                    <div className="job-listings-sec">
                                        {jobs.map(job => (
                                            <div className="job-listing">
                                                <div className="job-title-sec">
                                                    <div className="c-logo"> <img src="../../images/job_icon.png" alt="" /> </div>
                                                    <h3><Link to={"/job/"+(job.id)+"/"+(job.link)}>{job.name}</Link></h3>
                                                </div>
                                                <span className="job-lctn"><i className="la la-map-marker"/>{job.location}</span>
                                                <span className="job-is ft">{job.job_type}</span>
                                            </div>
                                        ))}
                                    </div>
                                    <div className=" w3-right">
                                        <Pagination
                                            activePage={this.state.activePage}
                                            itemsCountPerPage={10}
                                            totalItemsCount={this.state.total}
                                            pageRangeDisplayed={5}
                                            onChange={this.handlePageChange}
                                        />
                                    </div>{/*<!-- Pagination -->*/}
                                </div>
                            </div>
                        </div>
                    </div>

                </section>
            </div>
        );
    }
}}

export default Jobs;


