import React from 'react';
import EmployerHeader from "./layouts/header";
import Side from "./layouts/siderbar";
import {GetData} from "../services/getData";


class ManageJobs extends React.Component {

    constructor(props){
        super(props);
        this.state = {
            jobs:[],
            preloader: false,
        };
    };

    componentDidMount(){
        GetData("/jobs/employer").then((result)=>{
            if(result.status===1){
                this.setState({jobs:result.data,preloader: true,})
            }
            else {
                alert(result.error)
            }
            console.log(result.data)
        });
    }

    render() {
        const {error, preloader, jobs} = this.state;
        if (error) {
            return <div>Error</div>;
        } else if (!preloader) {
            return (
                <div id="loader-wrapper">
                    <div id="loader"/>
                    <div className="loader-section section-left"/>
                    <div className="loader-section section-right"/>
                </div>

            );
        } else {
            return (
                <div>
                    <EmployerHeader/>
                    <section>
                        <div className="block no-padding">
                            <div className="container">
                                <div className="row no-gape">
                                    <Side/>
                                    <div className="col-lg-9 column">
                                        <div className="padding-left">
                                            <div className="manage-jobs-sec">
                                                <h3>Manage Jobs</h3>
                                                {/*<div className="extra-job-info">
                                                <span><i className="la la-clock-o"/><strong>{jobs.length}</strong> Job Posted</span>
                                                <span><i className="la la-file-text"/><strong>20</strong> Application</span>
                                                <span><i className="la la-users"/><strong>18</strong> Active Jobs</span>
                                            </div>*/}
                                                {/*<div className="">*/}
                                                <table>
                                                    <thead>
                                                    <tr>
                                                        <td>Title</td>
                                                        <td>Applications</td>
                                                        <td>Created</td>
                                                        <td>Expire</td>
                                                        <td>Status</td>
                                                        <td>Action</td>
                                                    </tr>
                                                    </thead>
                                                    <tbody>
                                                    {jobs.map(job => (
                                                        <tr>
                                                            <td>
                                                                <div className="table-list-title">
                                                                    <h3><a href="" title="">{job.name}</a></h3>
                                                                </div>
                                                            </td>
                                                            <td>
                                                                <span
                                                                    className="applied-field">{job.applied} Applied</span>
                                                            </td>
                                                            <td>
                                                                <span>{job.created_at}</span><br/>
                                                            </td>
                                                            <td>
                                                                <span>{job.expired_at}</span><br/>
                                                            </td>
                                                            <td>
                                                                <span
                                                                    className="status active">{job.status === 0 ? "Pending" : job.status === 1 ? "Active" : "Expired"}</span>
                                                            </td>
                                                            <td>
                                                                <ul className="action_job">
                                                                    {/*<li><span>View Job</span><a href="" title=""><i className="la la-eye"></i></a></li>*/}
                                                                    <li><span>Edit</span><a href="" title=""><i
                                                                        className="la la-pencil"/></a></li>
                                                                    <li><span>Delete</span><a href="" title=""><i
                                                                        className="la la-trash-o"/></a></li>
                                                                </ul>
                                                            </td>
                                                        </tr>
                                                    ))}
                                                    </tbody>
                                                </table>
                                                {/*</div>*/}
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </section>
                </div>
            );
        }
    }
}

export default ManageJobs;


