import React from 'react';
import {LoginData} from "../services/LoginData";
import {PostData} from "../services/PostData";
import {Link} from "react-router-dom";
import {GetData} from "../services/getData";
import Logout from "./logout";


class PageLogin extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            grant_type : "password",
            client_id : "4",
            client_secret : "mF0aJxcqL8oK4eWnt8AAFxFdV8omQNsbuKtEeAJz",
            username: "",
            password: "",
            error: false,
            display:true,
            email:"",
            loading:false,


        }
    }

    login = (e) => {
        e.preventDefault();
        this.setState({loading:!this.state.loading});
        LoginData(this.state).then((result) =>{
            let authResult = result;
            if(authResult.access_token){
                this.setSession(authResult)
            }else {
                this.setState({loading:!this.state.loading});
                this.setState({error: 'Incorrect Email or Password!'})
            }

            // console.log(authResult)
        });
    };

    setSession = (authResult) => {
        // Set the time that the access token will expire at
        let expiresAt = JSON.stringify((authResult.expires_in * 1000) + new Date().getTime());
        localStorage.setItem('auth',JSON.stringify(authResult.token_type+" "+authResult.access_token));
        localStorage.setItem('expires_at', expiresAt);
        this.getUser();
    };

    getUser=()=>{
        GetData('/user').then((result)=>{
            if (result.id) {
                localStorage.setItem('user', JSON.stringify(result));
                if (localStorage.getItem('user')){
                    this.setState({loading:!this.state.loading});
                    window.location.reload()
                }
            }else {
                Logout();
            }

        })
    };

    forgotPassword=(e)=>{
        e.preventDefault();
        this.setState({loading:!this.state.loading});
        PostData('/api/password/token', this.state).then((result)=>{
            this.setState({loading:!this.state.loading});
        })
    };

    onChange =(e) =>{
        this.setState({[e.target.name]:e.target.value});
    };

    onClick=()=>{
        this.setState({display:!this.state.display})
    };


    render() {
        const loading= this.state.loading;
        return (
            <div className="account-popup-area signin-popup-box" style={{display:"block"}}>
                {this.state.display?
                    <div className="account-popup">
                        <Link to="/"><span className="close-popup"><i className="la la-close"/></span></Link>

                        <h3>User Login</h3>
                        <p className={"w3-text-red"}>{this.state.error}</p>
                        <form onSubmit={this.login}>
                            <div className="cfield">
                                <input type="email" placeholder="Email" name='username' onChange={this.onChange}/>
                                <i className="la la-user"/>
                            </div>
                            <div className="cfield">
                                <input type="password" placeholder="********" name='password' onChange={this.onChange}/>
                                <i className="la la-key"/>
                            </div>
                            <a onClick={this.onClick}>Forgot Password?</a>
                            <button disabled={loading}>{loading?<i className="la la-spinner la-spin"/>:"Login"}</button>
                            <div className="w3-right w3-margin-top">
                                don't have an account? <Link to="/signup" className="w3-text-red">sign up</Link>
                            </div>
                        </form>
                    </div>:
                    <div className="account-popup">
                        <Link to="/"><span className="close-popup"><i className="la la-close"/></span></Link>
                        <h3>Forgot Password</h3>
                        <p className={"w3-text-red"}>{this.state.error}</p>
                        <form onSubmit={this.forgotPassword}>
                            <div className="cfield">
                                <input type="email" placeholder="Email" name='email' onChange={this.onChange}/>
                                <i className="la la-envelope-o"/>
                            </div>
                            <button disabled={loading}>{loading?<i className="la la-spinner la-spin"/>:"Submit"}</button>
                            <a onClick={this.onClick}>Login</a>
                        </form>
                    </div>
                }
            </div>
        );
    }
}

export default PageLogin;


