import React from 'react';
import {Link} from "react-router-dom";

class Side extends React.Component {
    render() {
        return (
            <aside className="col-lg-3 column border-right">
                <div className="widget">
                    <div className="tree_widget-sec">
                        <ul>
                            <li className="">
                                <Link to="/profile"><i className="la la-file-text"/>Profile</Link>
                            </li>
                            <li className="">
                                <Link to="/dashboard/jobs"><i className="la la-briefcase"/>Manage Jobs</Link>
                            </li>
                            <li className="">
                                <Link to="/payment"><i className="la la-money"/>Payments</Link>
                            </li>
                            <li className="">
                                <Link to="/dashboard/package"><i className="la la-gift"/>My Package</Link>
                            </li>
                            <li className="">
                                <Link to="/post_job"><i className="la la-file-text"/>Post a New Job</Link>
                            </li>
                            <li className="">
                                <Link to="/change_password"><i className="la la-lock"/>Change Password</Link>
                            </li>
                            <li><Link to="/logout"><i className="la la-unlink"/>Logout</Link></li>
                        </ul>
                    </div>
                </div>
            </aside>

        );
    }
}

export default Side;


